const addGround = () => {
	let ground = Bodies.rectangle(worldWidth/2, height, worldWidth, height/2, { 
    isStatic: true,
    collisionFilter: {
      mask: 3
    }
  })
	ground.label = 'ground'
	World.add(world, ground)
	// dispatch(setGroundId(ground.id))
	return ground
}

