import styled from 'styled-components'

const Container = styled.div`
top: 0;
left: 0;
right: 0;
bottom: 0;
margin: auto;
display: block;
position: absolute;
height: ${ props => props.height }px;
width: ${ props => props.width }px;
background-color: ${ props => props['bgcolor'] ? props['bgcolor'] : 'none' };
`

const Backing = styled.div`
height: ${ props => props.height }px;
width: ${ props => props.width }px;
opacity: ${ props => props.opacity ? props.opacity : .5 };
background-color: ${ props => props.bgcolor ? props.bgcolor : '#222' };
`

const MenuTitle = styled.div`
left: 0;
right: 0;
top: 20px;
width: 300px;
margin: auto;
display: block;
font-size: 2em;
text-align: center;
position: absolute;
`

const MenuButtonContainer = styled.div`
top: ${ props => props['space-top'] ? props['space-top'] : 80 }px;
font-size: 1.5em;
text-align: center;
position: absolute;
width: 100%;
`

const MenuButton = styled.div`
opacity: ${ props => props['opacity'] ? props['opacity'] : 1 }
color: #fff;
position: relative;
width: ${ props => props['width'] ? props['width'] : 200}px;
height: ${ props => props['height'] ? props['height'] : 60 }px;
line-height: ${ props => props['height'] ? props['height'] : 60 }px;
background-color: ${ props => props['bgcolor'] ? props['bgcolor'] : '#00589b' };
left: 0; right: 0;
cursor: pointer;
margin: auto;
border: 2px solid ${ props => props['bordercolor'] ? props['bordercolor'] : 'rgba(0,0,0,0)' };
transform: scale(1.00);
&:hover {
  ${ props => props && props.cancelTransition === true ? '' : 'transition: all .35s' };
  ${ props => props && props.cancelTransition === true ? '' : 'transform: scale(1.05)' };
  background-color: ${ props => props['hovercolor'] };
  border: 2px solid ${ props => props['hoverborder'] ? props['hoverborder'] : 'rgba(0,0,0,0)' };
}
`

const MenuButtonSpacer = styled.div`
padding: ${ props => props['padding'] ? props['padding'] : 10 }px;
`

export default {
  Container,
  Backing, 
  MenuTitle,
  MenuButtonContainer,
  MenuButton,
  MenuButtonSpacer
}
