import { World, Bodies, Body } from 'matter-js'
import * as PIXI from 'pixi.js'
import random from 'random'
import Rock from '../items/Rock'
import { playerCollisionOptions } from '../collisionOptions'

export default class WoodPlank {

  constructor(pos) {
    this.gameObjects = window.gameObjects
    const { to, delayedCall } = window.TweenLite
    let container = this.gameObjects.getKeyValue('game-pixi-container')
    let world = this.gameObjects.getKeyValue('world')
    this.woodPlanksSheet = this.gameObjects.getKeyValue('wood-planks-sheet')
    this.woodPlanks = this.gameObjects.getKeyValue('wood-planks')
    this.plankRange = [ '000.png', '001.png', '002.png' ]
    // this.planks = new Array(random.int(4, 8)).fill(1)
    // this.planks.map(plank => this.getPlank())
    let {x,y} = pos
    this.gfc = this.getPlank()
    this.gfc.width = 50
    this.gfc.height = 15
    this.gfcContainer = new PIXI.Container()
    this.gfcContainer.addChild(this.gfc)
    this.gfcContainer.pivot.x = this.gfcContainer.width * .5
    this.gfcContainer.pivot.y = this.gfcContainer.height * .5
    this.body = new Bodies.rectangle(x,y,50,15,{collisionFilter:playerCollisionOptions})
    this.body.torque = random.float(-0.5, 0.5)
    Body.applyForce(this.body, this.body.position, {
      x: random.float(-0.02, 0.02),
      y: random.float(-0.01, -0.02)
    })
    World.add(world, this.body)
    container.addChild(this.gfcContainer)
    this.destroy = () => {
      let idx = this.woodPlanks.indexOf(this)
      World.remove(world, this.body)
      container.removeChild(this.gfcContainer)
      if (idx > -1) {
        this.woodPlanks.splice(idx, 1)
      }
      world = null
      container = null
      this.gameObjects = null
      this.woodPlanks = null
      this.gfc = null
    }
    delayedCall(4, () => {
      to(this.gfcContainer, 2, { alpha: 0, onComplete: () => { this.destroy() } })
    })
    return this
  }

  animate(renderFn) {
    let {x,y,rotation:rot} = renderFn(this.body)
    this.gfcContainer.position = {x,y}
    this.gfcContainer.rotation = rot
  }

  getPlank() {
    return (
      new PIXI.Sprite(
        this.woodPlanksSheet.textures[
          this.plankRange[Math.floor(Math.random() * this.plankRange.length)]
        ]
      )
    )
  }

}