// React
import React, { useState } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import buildGame from './buildGame'
import GameStates, { SubStates } from './constants/GameStates'
import { startWave, waveDefeated } from './waves/waveManager'
import { updateWeaponsUI } from './ui/WeaponsUI'
import { createGenericMessage } from './createMessages'
import { addToTotalCredits, gameStateTransition, setWeaponEquipped, loadLocalStorageState } from './gameSlice'
import PauseMenu, { gotoMainMenu } from './ReactComponents/PauseMenu'
import ShopMenu, { CornerBackButton, Inline } from './ReactComponents/ShopMenu'
import ShopQuery from './ReactComponents/ShopQuery'
import RespawnMenu from './ReactComponents/RespawnMenu'
import BuildMenu from './ReactComponents/BuildMenu'
import DoneBarrierPlacementBanner from './ReactComponents/DoneBarrierPlacementBanner'
import MenuComponents from './ReactComponents/MenuComponents'
import { NoTransitionButton } from './ReactComponents/PauseMenu'
import AchievementsView from './ReactComponents/AchievementsView'
import { motion } from 'framer-motion'
import Axios from 'axios'
import saveGame from './utils/saveGame';

const useScores = (initial) => {

  const [scores, setScores] = React.useState(initial)

  return [
    scores,
    setScores
  ]

}

export const ScoresView = (scores) => {

  return (
    <MenuComponents.Container>
      <MenuComponents.MenuTitle>Top 100 Scores of All-time</MenuComponents.MenuTitle>
      <MenuComponents.MenuButtonContainer
      style={{
        height: 470,
        overflow: 'scroll',
      }}
      >
        {
          scores && scores.scores && scores.scores
          .sort((a, b) => b.score - a.score)
          .map(score => (
            <>
              <NoTransitionButton
              bgcolor={ 'rgba(0, 0, 0, 0.25)' }
              hovercolor={ 'rgba(0, 0, 0, 0.35)' }
              bordercolor={ 'rgba(0, 0, 0, 0)' }
              hoverborder={ 'rgba(0, 0, 0, 0)' }
              >
                <Inline>
                  <div
                  style={{
                    left: '20px',
                    position: 'absolute',
                  }}>
                    { score.name }
                  </div>
                </Inline>
                <Inline>
                  <div
                  style={{
                    left: 0,
                    right: 0,
                  }}>
                    { score.score }
                  </div>
                </Inline>
                <Inline>
                  <div
                  style={{
                    right: '20px',
                    position: 'absolute',
                  }}>
                    {
                      (() => {
                        let date = score.time.substr(0, score.time.indexOf('T'))
                        let year = date.substr(0, score.time.indexOf('-'))
                        let dateNoYear = date.substr(score.time.indexOf('-') + 1, date.length)
                        return (
                          `${dateNoYear} - ${year}`
                        )
                      })()
                    }
                  </div>
                </Inline>
              </NoTransitionButton>            
            </>
          ))
        }
      </MenuComponents.MenuButtonContainer>
      <MenuComponents.MenuButtonContainer>
        <Inline>
          <CornerBackButton
          marginTop={'-60px'}
          onClick={ gotoMainMenu } >
            Refresh List
          </CornerBackButton>
        </Inline>
        <Inline>
          <CornerBackButton
          marginTop={'-60px'}
          onClick={ gotoMainMenu } />
        </Inline>
      </MenuComponents.MenuButtonContainer>
    </MenuComponents.Container>
  )

}

export default function Game () {

	const { gameObjects, TweenLite: { delayedCall } } = window
	const dispatch = useDispatch()
	const game = useSelector(state => state.game)
  const weaponEquipped = useSelector(state => state.game.player.weapons.equipped)
  const waitInterval = React.useRef(null)
  const cancelWaveStart = React.useRef(false)
  const [scores, setScores] = useScores([])
  const [animateScore, setAnimateScore] = useState(false)
  // const [upgradeToggleAnimate, setUpgradeToggleAnimate] = useState(false)
  const [runUpgraded, setRunUpgraded] = useState(false)
  const [reloadUpgraded, setReloadUpgraded] = useState(false)
  const [jumpUpgraded, setJumpUpgraded] = useState(false)
  const [meleeUpgraded, setMeleeUpgraded] = useState(false)
  const [defenseUpgraded, setDefenseUpgraded] = useState(false)
  // const equipped = useSelector(state => state.game.player.weapons.equipped)

	// one-time methods and non-react code will go here
	React.useEffect(() => {

    buildGame()

  }, []) // do once

	React.useEffect(() => {

		// on weapon toggle change..
		updateWeaponsUI()

	}, [weaponEquipped])

	React.useEffect(() => {

		if (game.gameSubState === SubStates.WAVE_STARTED) {
      startWave()
		}

		if (game.gameSubState === SubStates.WAVE_DEFEATED) {
			waveDefeated()
    }
    
    if (game.gameSubState === SubStates.WAITING) {
      let countdown = 3, timeInt = 500
      cancelWaveStart.current = false
      waitInterval.current = setInterval(() => {
        createGenericMessage(`${countdown}`, '#FFFFFF', .25, .5)
        countdown -= 1
      }, timeInt)
      delayedCall((timeInt/1000) * 4, () => {
        if (!cancelWaveStart.current) {
          clearInterval(waitInterval.current)
          dispatch(gameStateTransition('sub', game.gameSubState, SubStates.WAVE_STARTED))
        }
      })
    }

    if (game.gameSubState === SubStates.PLAYER_DEATH) {
      clearInterval(waitInterval.current)
      cancelWaveStart.current = true
			createGenericMessage('You died.', '#FF0000', .75, 3)
    }
    
  }, [game.gameSubState, delayedCall, dispatch])
  
	React.useEffect(() => {

		if (game.waveCountToDefeat === 0 && game.gameSubState !== SubStates.SHOP_QUERY_SUBMENU) {
			waveDefeated()
		}

	}, [game.waveCountToDefeat, game.lastGameSubState, game.gameSubState, dispatch])
	
	React.useEffect(() => {

    console.log('Game state has changed')

    if (game.gameState === GameStates.SHOP_MENU || game.gameState === GameStates.BUILD_MENU) {
      let screenUI = gameObjects.getKeyValue('screen-ui')
      screenUI.pause()
    }
    
    if (game.gameState === GameStates.MAIN_MENU || game.gameState === GameStates.GAMEPLAY) {
      document.getElementsByClassName('App')[0].style.pointerEvents = 'none'
    } else {
      document.getElementsByClassName('App')[0].style.pointerEvents = 'all'
    }

		if (game.gameState === GameStates.GAMEPLAY) {
			if (game.player.weapons.equipped === null) {
				dispatch(setWeaponEquipped('pistol weapon'))
			}
    }

  }, [game.gameState, game.player.weapons.equipped, gameObjects, dispatch])

  React.useEffect(() => {
    if (game.gameState !== GameStates.MAIN_MENU && game.purchases !== null &&
      game.lastPurchase && game.lastPurchase.item !== null && game.lastPurchase.amount > 0) {
      let snd = window.gameObjects.getKeyValue('purchase-sound')
      snd.play()
      let confirm = window.gameObjects.getKeyValue('confirm-sound')
      confirm.play()
      toast.dark(`You bought ${game.lastPurchase.item} for $${game.lastPurchase.amount}`, {
        position: "top-right",
        autoClose: 5000,
        hideProgressBar: true,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      })
      saveGame()
    }
  }, [ game.purchases, game.lastPurchase ])

  React.useEffect(() => {
    if (game.lastAchievementEarned !== null && game.gameState !== GameStates.MAIN_MENU) {
      let snd = window.gameObjects.getKeyValue('achievement2-sound')
      snd.play()
      toast.warning(`Achievement Earned: ${game.lastAchievementEarned}`, {
        position: "top-left",
        autoClose: 5000,
        hideProgressBar: true,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      })
      dispatch(
        addToTotalCredits( 500 )
      )
      saveGame()
    }
  }, [ game.achievements, game.lastAchievementEarned ])

  React.useEffect(() => {

    let isErr
    if (game.gameState === GameStates.SCORES_VIEW) {
      Axios.get('http://localhost:8080/scores')
      .catch(err => {
        console.log(err)
        isErr = true
      })
      .then(scores => {
        if (isErr) {
          console.log('api is on a different port, use yarn build to view working scores')
        } else {
          setScores( scores.data )
        }
      })
    }

  }, [ game.gameState ])

  React.useEffect(() => {

    setAnimateScore(true)
    delayedCall(.1, () => { setAnimateScore(false) })

  }, [ game.scoreToggle ])

  React.useEffect(() => {
    setRunUpgraded(true)
    delayedCall(.25, () => { setRunUpgraded(false) })
  }, [ game.player.upgradeTiers.run.current ])

  React.useEffect(() => {
    setReloadUpgraded(true)
    delayedCall(.25, () => { setReloadUpgraded(false) })
  }, [ game.player.upgradeTiers.reload.current ])

  React.useEffect(() => {
    setJumpUpgraded(true)
    delayedCall(.25, () => { setJumpUpgraded(false) })
  }, [ game.player.upgradeTiers.jump.current ])

  React.useEffect(() => {
    setMeleeUpgraded(true)
    delayedCall(.25, () => { setMeleeUpgraded(false) })
  }, [ game.player.upgradeTiers.melee.current ])

  React.useEffect(() => {
    setDefenseUpgraded(true)
    delayedCall(.25, () => { setDefenseUpgraded(false) })
  }, [ game.player.upgradeTiers.defense.current ])

	return (
		<>
      <ToastContainer />
      <>
        {
          game.gameState === GameStates.SCORES_VIEW ?
          <ScoresView scores={ scores } />
          : null
        }
      </>
      <>
        {
          game.gameState === GameStates.MAIN_MENU && window.gameObjects ?
          <>
            <PauseMenu
            margintop={ true }
            hidetitle={ true }
            hidebg={ false }
            bgcolor={ '#222' }
            playbtn={ true }
            />
          </>
          : null
        }
      </>
      <>
        {
          game.gameSubState === SubStates.BUILD_PLACEMENT_MODE && game.gameState === GameStates.GAMEPLAY
          ?
            <>
              <DoneBarrierPlacementBanner />
            </>
          : null 
        }
      </>
      <>
        {
          gameObjects && game.gameState === GameStates.PAUSE
          ? <PauseMenu shop-query-visibility={ game.gameSubState === SubStates.SHOP_QUERY_SUBMENU || game.gameSubState === SubStates.SCORE_ENTRY } />
          : <></>
        }
      </>
      <>
        {
          game.gameState === GameStates.SHOP_MENU
          ? <ShopMenu />
          : <></>
        }
      </>
      <>
        {
          game.gameState === GameStates.BUILD_MENU
          ? <BuildMenu />
          : <></>
        }
      </>
      <>
        {
          game.gameSubState === SubStates.SHOP_QUERY_SUBMENU || game.gameSubState === SubStates.SCORE_ENTRY
          ? <ShopQuery />
          : <></>
        }
      </>
      <>
        {
          game.gameSubState === SubStates.PLAYER_DEATH
          ? <RespawnMenu />
          : null
        }
      </>
      
      <>
        {
          /* Wave count and zombies to defeat */
        }
        {
          game.gameState !== GameStates.MAIN_MENU && game.gameState !== GameStates.SCORES_VIEW ?
          <div
          style={{
            backgroundColor: '#333',
            position: 'absolute',
            margin: 'auto',
            width: '50%',
            height: '30px',
            lineHeight: '30px',
            top: '4px',
            left: 0, right: 0,
            textAlign: 'center',
            borderRadius: '2px'
          }}>
            <div style={{ display: 'inline-block' }} className='debug-ui'>
              wave no: {game.waveNum}
            </div>
            <div style={{ marginLeft: '100px', display: 'inline-block' }} className='debug-ui'>
              enemies to defeat: {game.waveCountToDefeat}
            </div>
          </div>
          : null
        }
        {
          game.gameState !== GameStates.MAIN_MENU && game.gameState !== GameStates.SCORES_VIEW ?
            <motion.div
            className='game-score'
            animate={{
              scale: animateScore ? [ 1.35, 1.0 ] : 1.0
            }}
            transition={{
              duration: .35
            }}
            >
              {
                <span
                style={{
                  color: (
                    game.score < game.prevScore ? 'red'
                    : game.score > game.prevScore ? '#00FF61'
                    : 'white'
                  )
                }}>
                  {
                    game.score
                  }
                </span>
              }
            </motion.div>
          : null
        }
        <div
        className={ game.gameState !== GameStates.MAIN_MENU && game.gameState !== GameStates.SCORES_VIEW ? 'player-attributes' : '' }>
        {
          game.gameState !== GameStates.MAIN_MENU && game.gameState !== GameStates.SCORES_VIEW ?
          <>
            <div className={ reloadUpgraded ? 'upgrade-gui-transition' : 'upgrade-gui' }>
              Reload Lvl. { game.player.upgradeTiers['reload'].current }
            </div>
            <div className={ runUpgraded ? 'upgrade-gui-transition' : 'upgrade-gui' }>
              Run Lvl. { game.player.upgradeTiers['run'].current }
            </div>
            <div className={ jumpUpgraded ? 'upgrade-gui-transition' : 'upgrade-gui' }>
              Jump Lvl. { game.player.upgradeTiers['jump'].current }
            </div>
            <div className={ defenseUpgraded ? 'upgrade-gui-transition' : 'upgrade-gui' }>
              Defense Lvl. { game.player.upgradeTiers['defense'].current }
            </div>
            <div className={ meleeUpgraded ? 'upgrade-gui-transition' : 'upgrade-gui' }>
              Melee Lvl. { game.player.upgradeTiers['melee'].current }
            </div>
          </>
          : null
        }
        </div>
        {
          game.gameState !== GameStates.MAIN_MENU && game.gameState !== GameStates.SCORES_VIEW ?
            <div className='total-ammo-count-ui'>
              {
                game.player.weapons.totalAmmo[
                  game.player.weapons.equipped
                ]
              }
            </div>
          : null
        }
        {
          game.gameState !== GameStates.MAIN_MENU && game.gameState !== GameStates.SCORES_VIEW ?
          <div className='total-credits-ui'>
            credits: <span style={{ color: '#71DD4D' }}>${ game.player.credits }</span>
          </div>
          : null
        }
        {
          game.gameState !== GameStates.MAIN_MENU && game.gameState !== GameStates.SCORES_VIEW && weaponEquipped ?
          <div className='weapon-stats'>
            <div className='weapon-stats-item'>ROF: { game.player.modTiers['rof'][weaponEquipped].current }</div>
            <div className='weapon-stats-item'>PWR: { game.player.modTiers['powder'][weaponEquipped].current }</div>
            <div className='weapon-stats-item'>ACC: { game.player.modTiers['sight'][weaponEquipped].current }</div>
            <div className='weapon-stats-item'>CAP: { game.player.modTiers['mag'][weaponEquipped].current }</div>
          </div>
          : null
        }
      </>
		</>
	)

}
