import { Bodies, World, Constraint } from 'matter-js'
import { AnimatedSprite, Container, Text } from 'pixi.js'
import { playerCollisionOptions } from '../collisionOptions'
import GrenadeExplosion from '../FX/GrenadeExplosion'
import { getBodyWidth, getBodyHeight } from '../utils/Utils'

export default class RemoteMine {

  constructor(pos, mineNumber) {
    let { x, y } = pos
    let { gameObjects } = window
    this.world = gameObjects.getKeyValue('world')
    this.container = gameObjects.getKeyValue('game-pixi-container')
    this.buildObjects = gameObjects.getKeyValue('build-objects')
    this.remoteMines = gameObjects.getKeyValue('remote-mines')
    this.remoteMinesArr = gameObjects.getKeyValue('remote-mines-array')
    let landmineSheet = gameObjects.getKeyValue('remote-mine-sheet')
    let w = 100
    let h = 100
    this.body = Bodies.rectangle(x, y, w, 5, { collisionFilter: playerCollisionOptions })
    this.body.label = 'remote mine trap'
    this.body.destroy = () => { this.destroy() }
    // attach trap to the ground
    let ground = gameObjects.getKeyValue('ground')
    this.constraint = Constraint.create({
      bodyA: ground,
      bodyB: this.body,
      pointA: { x: -getBodyWidth(ground)/2 + pos.x, y: -getBodyHeight(ground)/2 },
      pointB: { x: 0, y: getBodyHeight(this.body)/2 },
      length: 0
    })
    World.add(this.world, [this.body, this.constraint])

    this.gfc = new AnimatedSprite(
      landmineSheet.animations['remote-mine']
    )
    this.gfc.updateAnchor = true
    this.gfc.loop = true
    this.gfc.animationSpeed = .75
    this.gfc.play()
    this.gfc.width = w
    this.gfc.height = h
    this.gfcContainer = new Container()
    this.gfcContainer.addChild(this.gfc)
    this.gfcContainer.pivot.x = 0
    this.gfcContainer.pivot.y = 0
    this.container.addChild(this.gfcContainer)
    console.log( this.gfcContainer.position )

    // attach a number to the mine to know which key to press to detonate it
    let txtStyle = { fill: '#00FFFF', fontSize: 12 }
    this.num = new Text(`[ ${mineNumber} ]: detonate`, txtStyle)
    this.num.pivot.x = this.num.width * .5
    this.num.pivot.y = this.num.height * .5
    this.container.addChild(this.num)
    
    this.destroyed = false

    return this
  }

  getBody() {
    return this.body
  }

  updatePosition(renderFn) {
    let { x, y } = renderFn(this.body)
    this.gfcContainer.position = { x, y }
    this.num.position = { x, y: y - 50 }
  }

  detonate() {
    this.destroy()
  }

  updateNumber(num) {
    this.num.text = `[ ${num} ]: detonate`
  }

  destroy() {
    let { TweenLite: { delayedCall } } = window
    new GrenadeExplosion(this.body.position, true)
    // place hitbox for explosion
    let { x:_x, y:_y } = this.body.position
    this.hitbox = Bodies.rectangle(_x, _y, 500, 300)
    this.hitbox.label = 'landmine explosion'
    this.hitbox.removeSelf = () => { World.remove(this.world, this.hitbox) }
    World.add(this.world, this.hitbox)
    delayedCall(.1, () => { this.hitbox.removeSelf() })
    if (!this.destroyed) {
      this.destroyed = true
      World.remove(this.world, [this.body, this.constraint])
      this.container.removeChild(this.gfcContainer)
      this.container.removeChild(this.num)
      let idx = this.buildObjects.indexOf(this)
      if (idx > -1) {
        this.buildObjects.splice(idx, 1)
      }
      let ridx = this.remoteMinesArr.indexOf(this)
      if (ridx > -1) {
        this.remoteMinesArr.splice(idx, 1)
      }
    }
  }

}