import random from 'random'
import * as PIXI from 'pixi.js'
import { GlowFilter } from '@pixi/filter-glow';
import { AdjustmentFilter } from '@pixi/filter-adjustment';
import store from '../../../app/store'

export default class MuzzleFlashFX {

  constructor() {
    let { gameObjects } = window
    this.to = window.TweenLite.to
    this.set = window.TweenLite.set
    let container = gameObjects.getKeyValue('game-pixi-container')
    this.container = container
    let muzzleFlashesSheet = gameObjects.getKeyValue('muzzle-flashes-sheet')
    let muzzleFlash001 = new PIXI.Sprite(
      muzzleFlashesSheet.textures['muzzle-flash1.png']
    )
    let muzzleFlash002 = new PIXI.Sprite(
      muzzleFlashesSheet.textures['muzzle-flash2.png']
    )
    let muzzleFlash003 = new PIXI.Sprite(
      muzzleFlashesSheet.textures['muzzle-flash3.png']
    )
    let muzzleFlash004 = new PIXI.Sprite(
      muzzleFlashesSheet.textures['muzzle-flash4.png']
    )
    let muzzleFlash005 = new PIXI.Sprite(
      muzzleFlashesSheet.textures['muzzle-flash5.png']
		)
		let muzzleFlashes = [
			muzzleFlash001,
			muzzleFlash002,
			muzzleFlash003,
			muzzleFlash004,
			muzzleFlash005
		]
    container.addChild(muzzleFlash001)
    container.addChild(muzzleFlash002)
    container.addChild(muzzleFlash003)
    container.addChild(muzzleFlash004)
    container.addChild(muzzleFlash005)
    muzzleFlash001.visible = false
    muzzleFlash002.visible = false
    muzzleFlash003.visible = false
    muzzleFlash004.visible = false
    muzzleFlash005.visible = false
    // muzzle fire FX 
    this.muzzleScale = .65
    this.blurFilter = new PIXI.filters.BlurFilter()
    this.blurFilter.blur = this.getRandomBlur()
    this.blurFilter.quality = 5
    this.glowFilter = new GlowFilter()
    this.glowFilter.innerStrength = 0.1
    this.glowFilter.outerStrength = random.float(1.0, 1.5)
    // this.glowFilter.color = 0xFFFFFF
    this.adjustmentFilter = new AdjustmentFilter()
    this.adjustmentFilter.brightness = 2.0
    this.adjustmentFilter.saturation = 2.0
    this.flashes = []
    muzzleFlashes.forEach(flash => {
      flash.updateAnchor = true
      flash.scale = { x: this.muzzleScale, y: this.muzzleScale }
      flash.filters = store.getState().game.filterEffects ? [
        this.blurFilter,
        this.adjustmentFilter,
        this.glowFilter
      ] : []
      this.flashes.push( flash )
    })
  }

  get muzzleScale () {
    return this._muzzleScale
  }

  set muzzleScale (scale) {
    this._muzzleScale = scale
  }

  fireEffect(pos) {
    let equipped = store.getState().game.player.weapons.equipped
    let tier = store.getState().game.player.modTiers['powder'][equipped].current
    let randFlash = this.getRandomFlash()
    randFlash.visible = true
    if (pos) {
      randFlash.position = pos
    }
    if (store.getState().game.filterEffects) {
      this.adjustmentFilter.saturation = (
        tier === 1 ? 1.0
        :
          tier === 2 ? 2.0
          :
            tier === 3 ? 3.0
            :
              tier === 4 ? 5.0
              : 1.0
      )
      this.glowFilter.color = (
        tier === 1 ? 0xFFF500
          :
            tier === 2 ? 0xFFD100
            :
              tier === 3 ? 0xFF6D00
              :
                tier === 4 ? 0xFF3800
                : 0xFFFFFF
      )
    }
    randFlash.filters = store.getState().game.filterEffects ? [
      this.glowFilter,
      this.adjustmentFilter,
      this.blurFilter
    ] : []
    this.container.addChild( randFlash )
    this.set(randFlash, { alpha: 1 })
    this.set(randFlash.scale, { x: this.muzzleScale })
    this.to(randFlash, .1, { alpha: 0 })
    this.to(randFlash.scale, .1, { x: this.muzzleScale * 2, onComplete: () => {
      randFlash.visible = false
    }})
  }

  setMuzzleRotations(rotation) {
    this.flashes.forEach(flash => {
      flash.rotation = rotation
    })
    this.rotation = rotation
  }

  getRandomBlur() {
    return random.float(0.5, 4.0)
  }

  changeBlurAmount() {
    this.blurFilter.blur = this.getRandomBlur()
  }

  getFlashes() {
    return this.flashes
  }

  getRandomFlash() {
    return this.flashes[ Math.floor( Math.random() * this.flashes.length) ]
  }

}