import * as PIXI from 'pixi.js'
import { makePixiSprite } from '../utils/Utils'
import { WEAPON_TYPE_SMG, WEAPON_TYPE_PISTOL, WEAPON_TYPE_SHOTGUN } from '../constants/weapons'
import { decrementTotalAmmo, setWeaponEquipped } from '../gameSlice'
import store from '../../../app/store'
import GameStates from '../constants/GameStates'
import { BevelFilter } from '@pixi/filter-bevel'
import { NoiseFilter } from '@pixi/filter-noise'
import { DropShadowFilter } from '@pixi/filter-drop-shadow'
 
const { getState, dispatch } = store

const { TweenLite: { to, from } } = window

export default function WeaponsUI () {
	let { gameObjects } = window
	let container = gameObjects.getKeyValue('game-pixi-container')
	let gameWidth = gameObjects.getKeyValue('game-width')
	let gameHeight = gameObjects.getKeyValue('game-height')
	let pistolIcon = makePixiSprite('weapon-ui-icon-pistol')
	let smgIcon = makePixiSprite('weapon-ui-icon-smg')
	let shotgunIcon = makePixiSprite('weapon-ui-icon-shotgun')
  let icons = []

  let mags
  
  let weaponShadow = new DropShadowFilter({ distance: 3.0, alpha: 1.0 })
  let bulletShadow = new DropShadowFilter({ distance: 1.0, alpha: 2.0 })

  let iconBG = new PIXI.Graphics()
  iconBG.beginFill(0x425041)
  iconBG.drawRect(0, 0, 120, 90)
  iconBG.alpha = .65
  iconBG.position.x = gameWidth - iconBG.width - 20
  iconBG.position.y = gameHeight - iconBG.height - 20
  // iconBG.filters = [ bulletShadow ]
  container.addChild(iconBG)

	pistolIcon.width = pistolIcon.width/2
	pistolIcon.height = pistolIcon.height/2
	icons.push(pistolIcon)

	smgIcon.width = smgIcon.width/2
	smgIcon.height = smgIcon.height/2
  icons.push(smgIcon)
  
  shotgunIcon.width = shotgunIcon.width/2
	shotgunIcon.height = shotgunIcon.height/2
  icons.push(shotgunIcon)

	icons.forEach(icon => {
		icon.position.x = gameWidth - (icon.width + 20)
		icon.position.y = gameHeight - (icon.height + 10)
    icon.alpha = 0
    // icon.filters = [ weaponShadow ]
		container.addChild(icon)
  })

  this.keepOnTop = () => {
    container.addChild(iconBG)
    icons.forEach(icon => { container.addChild(icon) })
    magazinesByWeaponType[WEAPON_TYPE_PISTOL]
    .concat(
      magazinesByWeaponType[WEAPON_TYPE_SMG]
    )
    .concat(
      magazinesByWeaponType[WEAPON_TYPE_SHOTGUN]
    )
    .forEach(b => { container.addChild(b) })
    this.grenades.forEach(g => { container.addChild(g) })
    if (this.shaking && !this.reloading) {
      container.addChild(this.emptyText)
    }
  }
  setInterval(this.keepOnTop, 1000)

  let magazinesByWeaponType = {
		'pistol weapon': null,
		'smg weapon': null,
		'shotgun weapon': null,
  }

  let bulletMask = new PIXI.Graphics()
  bulletMask.beginFill(0xffffff)
  bulletMask.drawRect(pistolIcon.position.x, pistolIcon.position.y, pistolIcon.width, pistolIcon.height)
  container.addChild(bulletMask)

  if (!window.gameObjects.getKeyValue('bullet-shell-bevel-ui')) {
    window.gameObjects.setKeyValue('bullet-shell-bevel-ui', new BevelFilter({ lightAlpha: .5, shadowAlpha: .5, thickness: .5 }))
  }
  let bulletBevel = window.gameObjects.getKeyValue('bullet-shell-bevel-ui')

  let makePistolMag = () => {
    magazinesByWeaponType[WEAPON_TYPE_PISTOL] = magazinesByWeaponType[WEAPON_TYPE_PISTOL].map((bullet, i) => {
      let bulletGfc = new PIXI.Graphics()
      bulletGfc.lineStyle(1, 0x564F2D)
      bulletGfc.beginFill(0xBFA522)
      bulletGfc.drawRect(0, 0, 4, 8)
      // let bevel = new BevelFilter({ lightAlpha: .5, shadowAlpha: .5, thickness: .5 })
      bulletGfc.filters = getState().game.filterEffects ? [ bulletBevel ] : []
      bulletGfc.alpha = 0
      bulletGfc.position.x = gameWidth - 27 - pistolIcon.width/magazinesByWeaponType[WEAPON_TYPE_PISTOL].length * i
      bulletGfc.position.y = pistolIcon.position.y + pistolIcon.height - 22
      container.addChild(bulletGfc)
      bulletGfc.mask = bulletMask
      return bulletGfc
    })
  }
  
  let makeSMGMag = () => {
    magazinesByWeaponType[WEAPON_TYPE_SMG] = magazinesByWeaponType[WEAPON_TYPE_SMG].map((bullet, i) => {
      let bulletGfc = new PIXI.Graphics()
      bulletGfc.lineStyle(1, 0x564F2D)
      bulletGfc.beginFill(0xCEC182)
      bulletGfc.drawRect(0, 0, 2, 8)
      // let bevel = new BevelFilter({ lightAlpha: .5, shadowAlpha: .5 })
      // bulletGfc.filters = [ bevel ]
      bulletGfc.alpha = 0
      bulletGfc.position.x = gameWidth - 24 - smgIcon.width/magazinesByWeaponType[WEAPON_TYPE_SMG].length * i
      bulletGfc.position.y = smgIcon.position.y + smgIcon.height - 22
      container.addChild(bulletGfc)
      bulletGfc.mask = bulletMask
      return bulletGfc
    })
  }
  
  let makeShotgunMag = () => {
    magazinesByWeaponType[WEAPON_TYPE_SHOTGUN] = magazinesByWeaponType[WEAPON_TYPE_SHOTGUN].map((bullet, i) => {
      let bulletGfc = new PIXI.Graphics()
      bulletGfc.lineStyle(1, 0x3E1313)
      bulletGfc.beginFill(0xB00000)
      bulletGfc.drawRect(0, 0, 4, 8)
      bulletGfc.beginFill(0xBCA845)
      bulletGfc.drawRect(0, 8, 4, 3)
      // let bevel = new BevelFilter({ lightAlpha: .5, shadowAlpha: .5, thickness: .25 })
      bulletGfc.filters = getState().game.filterEffects ? [ bulletBevel ] : []
      bulletGfc.alpha = 0
      bulletGfc.position.x = gameWidth - 32 - shotgunIcon.width/magazinesByWeaponType[WEAPON_TYPE_SHOTGUN].length * i
      bulletGfc.position.y = shotgunIcon.position.y + shotgunIcon.height - 26
      container.addChild(bulletGfc)
      bulletGfc.mask = bulletMask
      return bulletGfc
    })
  }

  this.checkFilters = () => {
    if (getState().game.filterEffects) {
      // SMG bullets too small for bevel filter
      magazinesByWeaponType[WEAPON_TYPE_PISTOL].forEach(b => { b.filters = [ bulletBevel ] })
      magazinesByWeaponType[WEAPON_TYPE_SHOTGUN].forEach(b => { b.filters = [ bulletBevel ] })
      if (this.grenades && this.gbevel && this.gnoise) {
        this.grenades.forEach(g => { g.filters = [ this.gbevel, this.gnoise ] })
      }
    } else {
      magazinesByWeaponType[WEAPON_TYPE_PISTOL].forEach(b => { b.filters = [] })
      magazinesByWeaponType[WEAPON_TYPE_SHOTGUN].forEach(b => { b.filters = [] })
      if (this.grenades) {
        this.grenades.forEach(g => { g.filters = [] })
      }
    }
  }
  
  let makeMagByType = {
    'pistol weapon': makePistolMag,
    'smg weapon': makeSMGMag,
    'shotgun weapon': makeShotgunMag,
  }

	let weaponIcons = {
		'pistol weapon': pistolIcon,
		'smg weapon': smgIcon,
		'shotgun weapon': shotgunIcon,
  }

  this.updateMagazine = type => {
    let { game: { player: { weapons: { ammoCapacity } } } } = getState()
    if (magazinesByWeaponType[type]) {
      magazinesByWeaponType[type].forEach(b => { b.destroy() })
      magazinesByWeaponType[type] = null
    }
    magazinesByWeaponType[type] = new Array(ammoCapacity[type]).fill(1)
    makeMagByType[type]()
  }

  // initialize magazines
  this.updateMagazine(WEAPON_TYPE_PISTOL)
  this.updateMagazine(WEAPON_TYPE_SMG)
  this.updateMagazine(WEAPON_TYPE_SHOTGUN)
  mags = [
    magazinesByWeaponType[WEAPON_TYPE_PISTOL],
    magazinesByWeaponType[WEAPON_TYPE_SMG],
    magazinesByWeaponType[WEAPON_TYPE_SHOTGUN]
  ]

  this.swapSwap = (wep) => {
    let { list, equipped } = getState().game.player.weapons
    magazineIndexes[wep] = 0
    let idx = list.indexOf(wep)
    if (idx > -1) {
      this.select(
        list[idx + 1] ?
        list[idx + 1] :
        list[0]
      )
    }
    window.TweenLite.delayedCall(.05, () => {
      this.select(equipped)
    })
  }

  this.updateWeaponMagazines = wep => {
    let { list } = getState().game.player.weapons
    mags.forEach(mag => { mag.forEach(b => { container.removeChild(b) }) })
    list.forEach(wep => { this.updateMagazine(wep) })
    mags = [
      magazinesByWeaponType[WEAPON_TYPE_PISTOL],
      magazinesByWeaponType[WEAPON_TYPE_SMG],
      magazinesByWeaponType[WEAPON_TYPE_SHOTGUN]
    ]
    this.reload(true)
    dispatch( setWeaponEquipped(wep) )
    this.swapSwap(wep) // switch to another weapon, the switch back to correct the ammo display
  }

	let magazineIndexes = {
		'pistol weapon': 0,
		'smg weapon': 0,
		'shotgun weapon': 0,
	}

	let magEmptyBools = {
		'pistol weapon': false,
		'smg weapon': false,
		'shotgun weapon': false,
  }

  this.checkEmpty = weapon_str => magEmptyBools[weapon_str]
  
  const doTheWiggle = (icon, defaultPos, callback) => {
    to(icon, .1, {
      x: defaultPos + 10,
      onComplete: () => {
        to(icon, .1, {
          x: defaultPos - 10,
          onComplete: () => {
            to(icon, .1, {
              x: defaultPos + 5,
              onComplete: () => {
                to(icon, .1, {
                  x: defaultPos - 5,
                  onComplete: () => {
                    to(icon, .1, {
                      x: defaultPos + 3,
                      onComplete: () => {
                        to(icon, .1, {
                          x: defaultPos - 2,
                          onComplete: () => {
                            to(icon, .1, {
                              x: defaultPos + 1,
                              onComplete: () => {
                                to(icon, .1, {
                                  x: defaultPos,
                                  onComplete: () => {
                                    this.shaking = false
                                    callback()
                                  }
                                })
                              }
                            })
                          }
                        })
                      }
                    })
                  }
                })
              }
            })
          }
        })
      }
    })
  }

  this.textForEmptyShake = 'RELOAD'
  this.changeTextForEmptyShake = str => {
    this.textForEmptyShake = str
    this.emptyText.text = str
  }

  this.emptyText = new PIXI.Text(this.textForEmptyShake, { fill: 0xB00000, fontSize: '15px', fontWeight: 'bold' })
  this.emptyText.pivot.x = this.emptyText.width * .5
  this.emptyText.pivot.y = this.emptyText.height * .5
  this.emptyText.position.x = iconBG.position.x + iconBG.width * .5
  this.emptyText.position.y = iconBG.position.y + iconBG.height * .8
  this.removeEmptyText = () => {
    container.removeChild(this.emptyText)
  }

  this.shaking = false
  this.emptyMagShake = () => {
    container.addChild(this.emptyText)
    if (!this.shaking) {
      this.shaking = true
      icons.forEach(icon => {
        let defaultPos = icon.position.x
        doTheWiggle(icon, defaultPos, this.removeEmptyText)
      })
      doTheWiggle(iconBG, iconBG.position.x, this.removeEmptyText)
    }
  }

	this.reloading = false
	this.reload = (forceReload) => {
		if (!this.reloading || forceReload) {
			let { game: { player: { weapons: { reloadAnimationFrames, equipped, totalAmmo } } } } = getState()
      this.removeEmptyText()
      if (magazineIndexes[equipped] !== 0 && totalAmmo[equipped] !== 0 || forceReload) {
        if (forceReload) { magazineIndexes[equipped] = 0 }
        this.reloading = true
				let ammoCheck = totalAmmo[equipped] < magazinesByWeaponType[equipped].length
				magazinesByWeaponType[equipped].forEach((b, i) => { b.alpha = 0 })
				for (let i = 0, limit = (ammoCheck ? totalAmmo[equipped] : magazinesByWeaponType[equipped].length); i < limit; ++i) {
					let b = magazinesByWeaponType[equipped][i]
					b.alpha = 1
					from(b, reloadAnimationFrames[equipped]/60, {
						x: gameWidth - (b.width * i),
						onComplete: (_i) => {
							if (_i === limit-1) {
								magazineIndexes[equipped] = 0
                magEmptyBools[equipped] = false
                this.lastBulletByWepType[equipped] = false
                if (forceReload) {
                  this.reloading = false
                }
							}
							if (totalAmmo[equipped] === 0) {
								this.reloading = false
							}
						},
						onCompleteParams: [i]
					})
				}
			}
		}
  }

  // forceWepEquipped is a weapon type string
	this.select = (forceWepEquipped) => {
    let { game: { player: { weapons: { totalAmmo, equipped } } } } = getState()
    icons.forEach(icon => { icon.alpha = 0 })
		mags.forEach(m => {
      m.forEach(b => {
        if (b && b.hasOwnProperty('alpha')) {
          b.alpha = 0
        }
      })
    })
		weaponIcons[forceWepEquipped ? forceWepEquipped : equipped].alpha = 1
		let ammoCheck = totalAmmo[forceWepEquipped ? forceWepEquipped : equipped] < magazinesByWeaponType[forceWepEquipped ? forceWepEquipped : equipped].length
		if (totalAmmo[forceWepEquipped ? forceWepEquipped : equipped] !== 0) {
			let limit = (ammoCheck
				? totalAmmo[forceWepEquipped ? forceWepEquipped : equipped]
				: magazinesByWeaponType[forceWepEquipped ? forceWepEquipped : equipped].length - magazineIndexes[forceWepEquipped ? forceWepEquipped : equipped])
			let lastMagIdx = ammoCheck ? magazinesByWeaponType[forceWepEquipped ? forceWepEquipped : equipped].length - totalAmmo[forceWepEquipped ? forceWepEquipped : equipped] : 0
			if (ammoCheck) { magazineIndexes[forceWepEquipped ? forceWepEquipped : equipped] = lastMagIdx }
			for (let i = 0; i < limit; ++i) {
				magazinesByWeaponType[forceWepEquipped ? forceWepEquipped : equipped][magazinesByWeaponType[forceWepEquipped ? forceWepEquipped : equipped].length-1 - i].alpha = 1
			}
    }
    // added this code to hide bullets when last mag is less than ammo cap. 
    if (magEmptyBools[equipped]) {
      magazinesByWeaponType[equipped].forEach(b => {
        if (!forceWepEquipped) {
          b.alpha = 0
        }
      })
    }
	}

  this.lastBulletByWepType = {
    'pistol weapon': false,
    'smg weapon': false,
    'shotgun weapon': false,
  }

	this.fireBullet = weapon_str => {
		if (magazinesByWeaponType[weapon_str][magazineIndexes[weapon_str]]) {
			magazinesByWeaponType[weapon_str][magazineIndexes[weapon_str]].alpha = 0
			magazineIndexes[weapon_str]++
			dispatch( decrementTotalAmmo(weapon_str) )
      if (magazinesByWeaponType[weapon_str].length === magazineIndexes[weapon_str]) {
        this.lastBulletByWepType[weapon_str] = true
      }
      if (magazinesByWeaponType[weapon_str].length === magazineIndexes[weapon_str]) {
				magEmptyBools[weapon_str] = true
			}
		}
  }
  
  const makeGrenadeIcon = () => {
    this.gbevel = new BevelFilter()
    this.gnoise = new NoiseFilter()
    this.gbevel.shadowAlpha = .45
    this.gbevel.lightAlpha = .35
    this.gbevel.thickness = 5
    this.gbevel.shadowColor = 0x0
    this.gbevel.lightColor = 0x208C44
    this.gnoise.noise = .1
    this.gnoise.seed = .5
    let grenadeGfc = new PIXI.Graphics()
    grenadeGfc.lineStyle(1, 0x0)
    grenadeGfc.beginFill(0x1F472C)
    grenadeGfc.drawCircle(0, 0, 6)
    grenadeGfc.filters = getState().game.filterEffects ? [ this.gbevel, this.gnoise ] : []
    container.addChild(grenadeGfc)
    
    return grenadeGfc
  }

  this.grenades = []

  this.updateGrenadeIcons = () => {
    if (this.grenades) { this.grenades.forEach(g => { g.destroy() }) }
    this.grenades = []
    let grenadeCount = getState().game.player.weapons.totalAmmo['grenades']
    for (let i = 0; i < grenadeCount; ++i) {
      let gnade = makeGrenadeIcon()
      this.grenades.push( gnade )
    }
    this.grenades.forEach((g, i) => {
      g.position.x = getState().game.width - 25 - ((g.width * 1.5) * i)
      g.position.y = getState().game.height - 110 - g.height
      from(g, (.1 * i), { y: g.position.y - 10, alpha: 0 })
    })
  }

  this.updateGrenadeIcons()

	return this
}

export const updateWeaponsUI = () => {
  let { gameObjects } = window
  let { game: { gameState } } = getState()
	if (gameObjects) {
		let weaponsUI = gameObjects.getKeyValue('weapons-ui')
		if (weaponsUI && (!weaponsUI.reloading || gameState === GameStates.SHOP_MENU)) {
      weaponsUI.select()
      gameObjects = null
		}
	}
}
