import store from '../../../app/store'
import { MOUSE_UP } from '../constants/mouseStates'
import { setMouseButtonState, unsetAllKeys } from '../gameSlice'

const { dispatch } = store

export default () => {
  dispatch( unsetAllKeys() )
  dispatch( setMouseButtonState(MOUSE_UP) )
  if (localStorage) {
    localStorage.setItem('ZombieChamber-GameSave', JSON.stringify(store.getState().game))
  }
}