import * as PIXI from 'pixi.js'
import random from 'random'
import { enemyCollisionOptions, playerCollisionOptions } from '../collisionOptions'
import { Body, Bodies, World, Composite, Constraint } from 'matter-js'
import { getBodyHeight } from '../utils/Utils'
import { decrementWaveCountToDefeat, increaseScore, incrementZombiesOnStage,
	updatePrevScore, setZombieLastKilled, setLastGrenadeKill, resetConsecutiveGrenadeKills, setAchievementOwned, increaseStats, StatsDefs } from '../gameSlice'
import { GibGenerator } from '../items/Gib'
import store from '../../../app/store'
import BloodSpurt from '../FX/BloodSpurt'
import { GenerateRandomAmmoDrops, GenerateRandomCreditDrops } from '../items/ItemGenerators'
import { BulletImpact } from '../player/fireWeapons'
import { GlowFilter } from '@pixi/filter-glow'
import HealthDrop from '../items/HealthDrop'
import AmmoDrop from '../items/AmmoDrop'
import { updateScore } from '../utils/updateScore'
import { degrees } from 'radians'
import { createTextMessage } from '../createMessages'
import { AchievementDefs } from '../gameSlice'
import AchievementsView from '../ReactComponents/AchievementsView'

export function ZombieRagdoll ({ x, y }, force, partLastDamaged) {
	let { dispatch, getState } = store
	let { TweenLite: { delayedCall, to } } = window
	let world, container, zombieRags
	let { gameObjects } = window
	if (!world) {
    world = gameObjects.getKeyValue('world')
  }
	if (!container) {
		container = gameObjects.getKeyValue('game-pixi-container')
	}
	if (!zombieRags) {
		zombieRags = gameObjects.getKeyValue('zombie-ragdolls')
	}
	let bloodSmack = gameObjects.getKeyValue('death-rip-sound')
	bloodSmack.play()
	let deathSnd = gameObjects.getKeyValue('zombie-death-sound')
	deathSnd.play()

	let b = { w: 40, h: 90 }
  let h = { w: 20, h: 30 }
	let a = { w: 15, h: 40 }
	let l = { w: 15, h: 40 }
	let hnd = { w: 15, h: 35 }
	let f = { w: 15, h: 35 }
	let bodyUpper = Bodies.rectangle(x, y, b.w, b.h, {
		collisionFilter: playerCollisionOptions
	})
	let head = Bodies.rectangle(x, y, h.w, h.h, {
		collisionFilter: playerCollisionOptions
	})
	let armUpperL = Bodies.rectangle(x, y, a.w, a.h, {
    collisionFilter: playerCollisionOptions
  })
	let armUpperR = Bodies.rectangle(x, y, a.w, a.h, {
    collisionFilter: playerCollisionOptions
	})
	let handL = Bodies.rectangle(x, y, hnd.w, hnd.h, {
		collisionFilter: playerCollisionOptions
	})
	let armLowerL = Bodies.rectangle(x, y, a.w, a.h, {
    collisionFilter: playerCollisionOptions
  })
	let armLowerR = Bodies.rectangle(x, y, a.w, a.h, { 
    collisionFilter: playerCollisionOptions
	})
	let handR = Bodies.rectangle(x, y, hnd.w, hnd.h, {
		collisionFilter: playerCollisionOptions
	})
	let legUpperL = Bodies.rectangle(x, y, l.w, l.h, {
    collisionFilter: playerCollisionOptions
  })
	let legUpperR = Bodies.rectangle(x, y, l.w, l.h, {
    collisionFilter: playerCollisionOptions
  })
	let legLowerL = Bodies.rectangle(x, y, l.w, l.h, {
    collisionFilter: playerCollisionOptions
  })
	let legLowerR = Bodies.rectangle(x, y, l.w, l.h, {
    collisionFilter: playerCollisionOptions
	})
	let footL = Bodies.rectangle(x, y, f.w, f.h, {
		collisionFilter: playerCollisionOptions
	})
	let footR = Bodies.rectangle(x, y, f.w, f.h, {
		collisionFilter: playerCollisionOptions
	})

	let bodyToHead = Constraint.create({
		bodyA: bodyUpper,
		bodyB: head,
		pointA: { x: 0, y: -b.h/2 },
		pointB: { x: 0, y: h.h/2 },
		length: 1,
	})
	let bodyUpperToArmUpperL = Constraint.create({
		bodyA: bodyUpper,
		bodyB: armUpperL,
		pointA: { x: -b.w/4, y: -b.h/2 + 10 },
		pointB: { x: 0, y: -a.h/2 },
		length: 2
	})
	let bodyUpperToArmUpperR = Constraint.create({
		bodyA: bodyUpper,
		bodyB: armUpperR,
		pointA: { x: 0, y: -b.h/2 + 10 },
		pointB: { x: 0, y: -a.h/2 },
		length: 2
	})
	let armUpperLToArmLowerL = Constraint.create({
		bodyA: armUpperL,
		bodyB: armLowerL,
		pointA: { x: 0, y: a.h/2 },
		pointB: { x: 0, y: -a.h/2 },
		length: 2
	})
	let armUpperRToArmLowerR = Constraint.create({
		bodyA: armUpperR,
		bodyB: armLowerR,
		pointA: { x: 0, y: a.h/2 },
		pointB: { x: 0, y: -a.h/2 },
		length: 2
	})
	let handToLowerL = Constraint.create({
		bodyA: armLowerL,
		bodyB: handL,
		pointA: { x: 0, y: a.h/2 },
		pointB: { x: 0, y: -hnd.h/2 },
		length: 1
	})
	let handToLowerR = Constraint.create({
		bodyA: armLowerR,
		bodyB: handR,
		pointA: { x: 0, y: a.h/2 },
		pointB: { x: 0, y: -hnd.h/2 },
		length: 1
	})
	let bodyUpperToLegUpperL = Constraint.create({
		bodyA: bodyUpper,
		bodyB: legUpperL,
		pointA: { x: -b.w/4, y: b.h/2 },
		pointB: { x: 0, y: -l.h/2 },
		length: 2
	})
	let bodyUpperToLegUpperR = Constraint.create({
		bodyA: bodyUpper,
		bodyB: legUpperR,
		pointA: { x: 0, y: b.h/2 },
		pointB: { x: 0, y: -l.h/2 },
		length: 2
	})
	let legUpperLToLegLowerL = Constraint.create({
		bodyA: legUpperL,
		bodyB: legLowerL,
		pointA: { x: 0, y: l.h/2 },
		pointB: { x: 0, y: -l.h/2 },
		length: 2
	})
	let legUpperRToLegLowerR = Constraint.create({
		bodyA: legUpperR,
		bodyB: legLowerR,
		pointA: { x: 0, y: l.h/2 },
		pointB: { x: 0, y: -l.h/2 },
		length: 2
	})
	let footLToLowerLegL = Constraint.create({
		bodyA: legLowerL,
		bodyB: footL,
		pointA: { x: 0, y: l.h/2 },
		pointB: { x: 0, y: -f.h/2 },
		length: 1
	})
	let footRToLowerLegR = Constraint.create({
		bodyA: legLowerR,
		bodyB: footR,
		pointA: { x: 0, y: l.h/2 },
		pointB: { x: 0, y: -f.h/2 },
		length: 1
	})

  this.composite = Composite.create()
  this.bodies = [
    head,
    armUpperL, armUpperR,
    armLowerL, armLowerR,
    legUpperL, legUpperR,
    legLowerL, legLowerR,
    footL, footR
	]
	this.bodies.forEach(body => { body.label = 'zombie body part' })
	bodyUpper.label = 'torso'
	Composite.add(this.composite, [
    head, bodyUpper,
    armUpperL, armUpperR, armLowerL, armLowerR,
		legUpperL, legUpperR, legLowerL, legLowerR,
		handL, handR, footL, footR,
		bodyToHead, handToLowerL, handToLowerR,
		footLToLowerLegL, footRToLowerLegR,
		bodyUpperToArmUpperL, bodyUpperToArmUpperR,
		armUpperLToArmLowerL, armUpperRToArmLowerR,
		bodyUpperToLegUpperL, bodyUpperToLegUpperR,
		legUpperLToLegLowerL, legUpperRToLegLowerR,
	])
	this.constraints = [
		bodyToHead,
		handToLowerL, handToLowerR,
		footLToLowerLegL, footRToLowerLegR,
		bodyUpperToArmUpperL, bodyUpperToArmUpperR,
		armUpperLToArmLowerL, armUpperRToArmLowerR,
		bodyUpperToLegUpperL, bodyUpperToLegUpperR,
		legUpperLToLegLowerL, legUpperRToLegLowerR,
	]

	World.add(world, this.composite)
  Body.applyForce(bodyUpper, bodyUpper.position, force)
  this.bodies.forEach(_body => {
    Body.applyForce(_body, _body.position, { x: random.float(-0.01, -0.05), y: random.float(-0.01, -0.05) })
  })
	zombieRags.push(this)

	// add sprites
	let z_parts_spritesheet = gameObjects.getKeyValue('zombie-parts-gored-sheet')

  const getSkullTex = () => {
    let skulls = [ 'skull_gored.png', 'skull_gored2.png', 'skull_gored3.png' ]
    return skulls[Math.floor(Math.random() * skulls.length)]
  }
	let skull = new PIXI.Sprite(
    z_parts_spritesheet.textures[ getSkullTex() ]
  )
	skull._id = 'skull'
	let torso = new PIXI.Sprite( z_parts_spritesheet.textures['torso_whole_gored.png'] )
	torso._id = 'torso'
	
	let spriteParts = []
  
  let leg2 = new PIXI.Sprite(
    z_parts_spritesheet.textures[`rear_leg_lower_gored.png`]
  )
  leg2._id = 'leg 2'
	spriteParts.push( leg2 )
	
	let hand2 = new PIXI.Sprite(
		z_parts_spritesheet.textures['rear_arm_hand_gored.png']
	)
	hand2._id = 'hand 2'
	spriteParts.push( hand2 )

  let arm2 = new PIXI.Sprite(
    z_parts_spritesheet.textures[`rear_arm_forearm_gored.png`]
  )
  arm2._id = 'arm 2'
  spriteParts.push( arm2 )

  let upperArm2 = new PIXI.Sprite(
		z_parts_spritesheet.textures[`rear_arm_upper_gored.png`]
	)
	upperArm2._id = 'limb'
  spriteParts.push( upperArm2 )
  
	let upperLeg2 = new PIXI.Sprite(
		z_parts_spritesheet.textures[`rear_leg_upper_gored.png`]
	)
	upperLeg2._id = 'limb'
	spriteParts.push( upperLeg2 )

	let foot2 = new PIXI.Sprite(
		z_parts_spritesheet.textures['rear_leg_foot_gored.png']
	)
	foot2._id = 'foot 2'
	spriteParts.push( foot2 )
  
  spriteParts.push( torso, skull )

	let leg1 = new PIXI.Sprite(
		z_parts_spritesheet.textures[`front_leg_lower_gored.png`]
	)
	leg1._id = 'leg 1'
  spriteParts.push( leg1 )
	
	let hand1 = new PIXI.Sprite(
		z_parts_spritesheet.textures['front_arm_hand_gored.png']
	)
	hand1._id = 'hand 1'
	spriteParts.push( hand1 )

	let arm1 = new PIXI.Sprite(
		z_parts_spritesheet.textures[`front_arm_forearm_gored.png`]
	)
	arm1._id = 'arm 1'
  spriteParts.push( arm1 )

  let upperArm1 = new PIXI.Sprite(
		z_parts_spritesheet.textures[`front_arm_upper_gored.png`]
	)
	upperArm1._id = 'limb'
  spriteParts.push( upperArm1 )

	let upperLeg1 = new PIXI.Sprite(
		z_parts_spritesheet.textures[`front_arm_upper_gored.png`]
	)
	upperLeg1._id = 'limb'
	spriteParts.push( upperLeg1 )
	
	let foot1 = new PIXI.Sprite(
		z_parts_spritesheet.textures['front_leg_foot_gored.png']
	)
	foot1._id = 'foot 1'
	spriteParts.push( foot1 )
	
	spriteParts.forEach(part => {
		part.updateAnchor = true
		part.pivot.x = part.width * .5
		part.pivot.y = part.height * .5
		container.addChild(part)
	})

	this.head = head
	this.bodyUpper = bodyUpper
	this.armUpperL = armUpperL
	this.armUpperR = armUpperR
	this.armLowerL = armLowerL
	this.armLowerR = armLowerR
	this.legUpperL = legUpperL
	this.legUpperR = legUpperR
	this.legLowerL = legLowerL
	this.legLowerR = legLowerR
	this.handL = handL
	this.handR = handR
	this.footL = footL
	this.footR = footR

	// check direction inderectly by checking the force applied to the ragdoll
	let gibForce = (
    force ? 
      force.x < 0 ?
      { x: -0.002 }
      : { x: 0.002 }
    : { x: random.float(-0.002, 0.002) }
  )
	gibForce.y = -0.005
	
	new GibGenerator(
		getState().game.lastWaveCountDefeated && getState().game.lastWaveCountDefeated < 10 ? random.int(5, 10) : 3,  //random.int(10, 15),
		bodyUpper.position,
		gibForce,
		getState().game.lastWaveCountDefeated && getState().game.lastWaveCountDefeated < 10 ? 2 : 1
  )

	this.destroy = () => {
		spriteParts.forEach((sprite, i) => {
      to(sprite, getState().game.lastWaveCountDefeated && getState().game.lastWaveCountDefeated < 10 ? 1 : .5, {
        alpha: 0,
        onComplete: () => {
          container.removeChild(sprite)
          if (i === spriteParts.length - 1) {
            World.remove(world, this.composite)
						zombieRags.splice(zombieRags.indexOf(this), 1)
						zombieRags = null
						world = null
						// container = null
						this.bodies.forEach(b => { b = null })
						this.constraints.forEach(c => { c = null })
          }
        }
      })
    })
	}
  
	this.animateRagParts = renderFnc => {
		
		let skullPos = renderFnc(this.head)
		skull.position = skullPos
		skull.rotation = skullPos.rotation
		let torsoPos = renderFnc(this.bodyUpper)
		torso.position = torsoPos
		torso.rotation = torsoPos.rotation

		let arm1Pos = renderFnc(this.armLowerL)
		arm1.position = arm1Pos
		arm1.rotation = arm1Pos.rotation
		let upperArm1Pos = renderFnc(this.armUpperL)
		upperArm1.position = upperArm1Pos
		upperArm1.rotation = upperArm1Pos.rotation
		let hand1Pos = renderFnc(this.handL)
		hand1.position = hand1Pos
		hand1.rotation = hand1Pos.rotation
		let upperLeg1Pos = renderFnc(this.legUpperL)
		upperLeg1.position = upperLeg1Pos
		upperLeg1.rotation = upperLeg1Pos.rotation
		let leg1Pos = renderFnc(this.legLowerL)
		leg1.position = leg1Pos
		leg1.rotation = leg1Pos.rotation
		let foot1Pos = renderFnc(this.footL)
		foot1.position = foot1Pos
		foot1.rotation = foot1Pos.rotation

		let arm2Pos = renderFnc(this.armLowerR)
		arm2.position = arm2Pos
		arm2.rotation = arm2Pos.rotation
		let upperArm2Pos = renderFnc(this.armUpperR)
		upperArm2.position = upperArm2Pos
		upperArm2.rotation = upperArm2Pos.rotation
		let hand2Pos = renderFnc(this.handR)
		hand2.position = hand2Pos
		hand2.rotation = hand2Pos.rotation
		let upperLeg2Pos = renderFnc(this.legUpperR)
		upperLeg2.position = upperLeg2Pos
		upperLeg2.rotation = upperLeg2Pos.rotation
		let leg2Pos = renderFnc(this.legLowerR)
		leg2.position = leg2Pos
		leg2.rotation = leg2Pos.rotation
		let foot2Pos = renderFnc(this.footR)
		foot2.position = foot2Pos
		foot2.rotation = foot2Pos.rotation

	}

	// detach a random constraint
	const randomConstraintToRemove = () => this.constraints[ Math.floor( Math.random() * this.constraints.length ) ]
	let parts = new Array(random.int(1, 10)).fill(1)
  parts = parts.map((item, i) => {
    let rand
    const getARandomConstraint = () => {
      rand = randomConstraintToRemove()
      if (parts.indexOf(rand) > -1) {
        getARandomConstraint()
      } else {
        return rand
      }
    }
    return getARandomConstraint()
  })
  parts.forEach(part => { Composite.remove(this.composite, part) })

	// auto removal
	delayedCall(
		!getState().game.lastWaveCountDefeated || getState().game.lastWaveCountDefeated < 10 ? 4 : 1,
		this.destroy
	)

	return this
}

export default function Zombie(x, y) {
	let { dispatch, getState } = store
	let { Power2, TweenLite: { delayedCall, to, set } } = window
	let world, zombies, container
	let { gameObjects } = window
  if (!world) {
    world = gameObjects.getKeyValue('world')
  }
  if (!zombies) {
    zombies = gameObjects.getKeyValue('zombies')
  }
  if (!container) {
    container = gameObjects.getKeyValue('game-pixi-container')
	}
	let bulletImpacts = gameObjects.getKeyValue('bullet-impacts')

  dispatch( incrementZombiesOnStage() )
	
  let b = { w: 60, h: 125 }
	let h = { w: 40, h: 60 }
	// let zombieWalkSpeed = .65
	let zombieWalkSpeed = getState().game.enemies.zombies.walkSpeed

  let body = Bodies.rectangle(x, y, b.w, b.h, { collisionFilter: enemyCollisionOptions })
  let head = Bodies.rectangle(x, y, h.w, h.h, { collisionFilter: enemyCollisionOptions, inertia: Infinity })
  
	body.label = 'enemy zombie body'
	head.label = 'enemy zombie head'

  let headToBody = Constraint.create({
    bodyA: head,
    bodyB: body,
    pointA: { x: 0, y: h.h/2 },
    pointB: { x: 0, y: -b.h/2 },
    length: 0,
  })

  let zombie = Composite.create()

  this.composite = zombie
  this.body = body
  this.head = head

  Composite.add(zombie, [
    body, head,
    headToBody
  ])
  World.add(world, zombie)

  // pixi stuff
  let zWalkSheet = gameObjects.getKeyValue('zombie-walk-sheet') //gameObjects.getKeyValue('z-walk-spritesheet')
	let zAttackSheet = gameObjects.getKeyValue('zombie-attack1-sheet')  // gameObjects.getKeyValue('z-attack-spritesheet')
	let idleSheet = gameObjects.getKeyValue('zombie-idle-sheet')
	let idleAnim = new PIXI.AnimatedSprite( idleSheet.animations['idle'] )
	let zWalk = new PIXI.AnimatedSprite(zWalkSheet.animations['walkcycle'])
	let zAttack = new PIXI.AnimatedSprite(zAttackSheet.animations['attack1'])
	let zAttack2 = new PIXI.AnimatedSprite(gameObjects.getKeyValue('zombie-attack2-sheet').animations['attackTwo'])
	
	// slash fx 
	let slashFX = gameObjects.getKeyValue('zombie-slash-fx-sheet')
  let slashAnim = new PIXI.AnimatedSprite( slashFX.animations['slash'] )
	
	slashAnim.loop = false
	slashAnim.animationSpeed = .5
	slashAnim.updateAnchor = true
	slashAnim.pivot.x = slashAnim.width * .5
	slashAnim.pivot.y = slashAnim.height * .5
	slashAnim.visible = false
	slashAnim.onComplete = () => {
		slashAnim.visible = false
	}
  container.addChild(slashAnim)
  
  // shadow
  this.shadow = new PIXI.Graphics()
  this.shadow.beginFill(0x0, 0.35)
  this.shadow.drawEllipse(0, 0, 80, 10)
  let blur = new PIXI.filters.BlurFilter()
	blur.blur = 8
  this.shadow.filters = getState().game.filterEffects ? [ blur ] : []
  if (container && container.children.indexOf(this.shadow) === -1) {
    container.addChild(this.shadow)
  }

	idleAnim.loop = true
	idleAnim.animationSpeed = .5
	idleAnim.updateAnchor = true
	idleAnim.pivot.x = idleAnim.width * .5
	idleAnim.pivot.y = idleAnim.height * .6
	container.addChild(idleAnim)

  zWalk.loop = true
  zWalk.animationSpeed = 0.5
  zWalk.updateAnchor = true
  zWalk.pivot.x = zWalk.width * .5
  zWalk.pivot.y = zWalk.height * .6
  container.addChild(zWalk)

	zAttack.loop = false
  zAttack.animationSpeed = 0.65
  zAttack.updateAnchor = true
  zAttack.pivot.x = zAttack.width * .5
  zAttack.pivot.y = zAttack.height * .6
  container.addChild(zAttack)
	zAttack.visible = false
	
	zAttack2.loop = false
  zAttack2.animationSpeed = 0.65
  zAttack2.updateAnchor = true
  zAttack2.pivot.x = zAttack2.width * .5
  zAttack2.pivot.y = zAttack2.height * .6
  container.addChild(zAttack2)
	zAttack2.visible = false

	this.walkAnim = zWalk
	this.attackAnim = zAttack
	this.attackAnim2 = zAttack2
	this.idleAnim = idleAnim

	this.anims = [
		this.walkAnim,
		this.attackAnim,
		this.attackAnim2,
		this.idleAnim
	]
	this.anims.forEach(anim => {
		window.TweenLite.from(
			anim, 1, { alpha: 0 }
		)
	})

	this.playerBodyPosition = null

	this.playSlashAnim = () => {
		if (this.attackAnim.visible) {
			slashAnim.position = {
				x: this.attackAnim.position.x,
				y: this.attackAnim.position.y - 50
			}
			// slashAnim.scale.x = 1
			slashAnim.scale.x = this.attackAnim.scale.x
			slashAnim.rotation = degrees(0)
		} else {
			slashAnim.position = {
				x: this.attackAnim.position.x,
				y: this.attackAnim.position.y - 50
			}
			// slashAnim.scale.x = -1
			slashAnim.scale.x = this.attackAnim.scale.x * -1
			slashAnim.rotation = degrees(180)
		}
		slashAnim.visible = true
		slashAnim.gotoAndPlay(0)
	}

	this.setSpritesPositions = fn => {
		let { x, y } = fn(this.body)
		this.shadow.position.x = x
		this.shadow.position.y = y + this.walkAnim.height/4 + 10
		this.walkAnim.position.x = x
		this.walkAnim.position.y = y
		this.attackAnim.position.x = x
		this.attackAnim.position.y = y
		this.attackAnim2.position.x = x
		this.attackAnim2.position.y = y
		this.idleAnim.position.x = x
		this.idleAnim.position.y = y	
		// set timescale according to whether player is alive
		if (this.player.dead) {
			this.walkAnim.animationSpeed = 0.2
			this.walkAnim.animationSpeed = 0.2
			this.attackAnim.animationSpeed = 0.2
			this.attackAnim2.animationSpeed = 0.2
			this.idleAnim.animationSpeed = 0.2
			slashAnim.animationSpeed = 0.2
		} else {
			this.walkAnim.animationSpeed = 0.5
			this.attackAnim.animationSpeed = 0.65
			this.attackAnim2.animationSpeed = 0.65
			this.idleAnim.animationSpeed = 0.5
			slashAnim.animationSpeed = 0.5
		}
		if (this.shouldDisplayHealth && this.healthDisplay !== undefined) {
			this.moveLifeDisplay( fn(this.body) )
		}
	}

	this.setPlayerVars = ({ player, playerDirection, playerBody }) => {
		this.player = player
		this.playerBodyPosition = playerBody.position
		this.playerBody = playerBody
		this.playerDirection = playerDirection
	}
	
	const zombieFarFromPlayer = (distance) => {
		return (this.playerBody.position.x - (distance ? distance : 120) > this.body.position.x || this.playerBody.position.x + (distance ? distance : 120) < this.body.position.x)
	}

	this.playIdleAnim = () => {
		if (!zAttack.playing && !zAttack2.playing && !this.walkAnimPlaying) {
			idleAnim.play()
			idleAnim.visible = true
		}
	}

	this.stopIdleAnim = () => {
		idleAnim.visible = false
	}

	this.walkAnimPlaying = false
	this.playWalkAnim = () => {
		if (this.walkAnimPlaying === false) {
			this.stopIdleAnim()
			this.walkAnim.visible = true
			this.walkAnim.play()
			this.walkAnimPlaying = true
		} else {
			this.playIdleAnim()
		}
	}
	this.stopWalkAnim = () => {
		if (this.walkAnimPlaying === true) {
			this.walkAnim.stop()
			this.walkAnim.gotoAndStop(0)
			this.walkAnimPlaying = false
			this.walkAnim.visible = false
			this.playIdleAnim()
		}
	}
  this.playWalkAnim()
  
  this.healthOffsetY = 160
  this.damageDisplayOptions = {
    fill: '#FF0000',
    fontSize: 11
  }
  this.healthDisplayOptions = {
    fill: '#FFFF00',
    fontSize: 11
  }
	this.healthDisplay = new PIXI.Text(this.health, this.healthDisplayOptions)
	this.healthDisplay.pivot.x = this.healthDisplay.width * .5
	if (getState().game.displayZombieHealth) {
		this.shouldDisplayHealth = true
		container.addChild(this.healthDisplay)
	}

  this.moveLifeDisplay = (renderFnResult) => {
		if (this.shouldDisplayHealth) {
			if (!this.dead) {
				this.healthDisplay.text = this.health
			} else {
				this.healthDisplay.text = 0
			}
			this.healthDisplay.position = {
				x: renderFnResult.x,
				y: renderFnResult.y - this.healthOffsetY
			}
		} else if (container.children.indexOf(this.healthDisplay) > -1) {
			container.removeChild(this.healthDisplay)
		}
  }

  // runs via the engine loop, so will repeat 30-60 times a second
  this.walk = () => {
		let comparison = (this.playerBody.position.x < this.body.position.x)
		if (zombieFarFromPlayer() && !zAttack.playing && !zAttack2.playing && !this.player.dead) {
			this.playWalkAnim()
      if (comparison) {
        this.sprites.forEach(sprite => { sprite.scale.x = 1 })
      } else {
        this.sprites.forEach(sprite => { sprite.scale.x = -1 })
      }
      let alteredWalkSpeed
      if (zombieFarFromPlayer(800)) {
        zWalk.animationSpeed = 1.00
        alteredWalkSpeed = zombieWalkSpeed * random.float(2.8, 3.2)
      } else if (zombieFarFromPlayer(400)) {
        zWalk.animationSpeed = .75
        alteredWalkSpeed = zombieWalkSpeed * random.float(1.8, 2.2)
      } else {
        zWalk.animationSpeed = 0.5
        alteredWalkSpeed = zombieWalkSpeed
      }
      Body.translate(this.body, { x: comparison ? -alteredWalkSpeed : alteredWalkSpeed, y: 0 })
		} else {
			this.stopWalkAnim()
		}
  }

	this.health = getState().game.enemies.zombies.startingHealth

	this.checkFilters = () => {
		// if (getState().game.filterEffects) {
		// 	this.shadow.filters = [ blur ]
		// 	this.shadow.alpha = 1.0
		// 	if (this.criticalText) {
		// 		this.criticalText.filters = [ this.criticalGlow ]
		// 	}
		// } else {
		// 	this.shadow.filters = []
		// 	this.shadow.alpha = .5
		// 	if (this.criticalText) {
		// 		this.criticalText.filters = []
		// 	}
		// }
	}

	// this.shouldDisplayHealth = true
	this.displayHealth = bool => {
		this.shouldDisplayHealth = bool
		if (bool) {
			container.addChild(this.healthDisplay)
		} else {
			if (container.children.indexOf(this.healthDisplay)) {
				container.removeChild(this.healthDisplay)
			}
		}
	}

	this.sprites = [
		this.walkAnim,
		this.attackAnim,
		this.attackAnim2,
		this.idleAnim,
    slashAnim
	]

	this.composite.bodies.forEach(body => {
		body.main = this
	})

	this.dead = false

  this.dmgTxts = []
	this.dmgIterator = 0
	this.tintAmounts = {
		1: 0xFF9C00,
		2: 0xFF2E00,
		3: 0xFF0000,
		4: 0xB7000F
	}
	this.takeDamage = (dmg, partDamaged, isCritical, isMelee, attackType) => {
		// randomize relative position of blood spurt 
		let spurtPos
		let spurtPosLow = {
			x: random.float(this.walkAnim.position.x - 20, this.walkAnim.position.x + 20),
			y: random.float(this.walkAnim.position.y, this.walkAnim.position.y + 50)
		}
		let spurtPosHi = {
			x: random.float(this.walkAnim.position.x - 20, this.walkAnim.position.x + 20),
			y: random.float(this.walkAnim.position.y - 100, this.walkAnim.position.y - 50)
		}
		if (!partDamaged || partDamaged === 'head') {
			spurtPos = spurtPosHi
		} else {
			spurtPos = spurtPosLow
		}
		new BloodSpurt( null, spurtPos )
		this.health -= dmg
		this.directionX = this.playerBodyPosition ? this.playerBodyPosition.x > this.body.position.x : false
		if (this.health <= 0 && !this.dead) {
			if (attackType === 'turret') {
				dispatch(
					increaseStats( StatsDefs.turretKills )
				)
			}
			this.kill(partDamaged, null, attackType ? attackType : null)
      if (isMelee) {
        dispatch(
          increaseStats( StatsDefs.meleeKills )
        )
        setTimeout(() => {
          if (getState().game.stats.meleeKills === 5 && !getState().game.achievements[ AchievementDefs.PILEDRIVE ].owned) {
            dispatch(
              setAchievementOwned( AchievementDefs.PILEDRIVE )
            )
          }
        }, 0)
      }
		}
		this.sprites.forEach(sprite => {
			if (sprite !== slashAnim) {
				if (this.health < 25) {
					sprite.tint = this.tintAmounts[3]
				}
				if (this.health < 10) {
					sprite.tint = this.tintAmounts[4]
				}
			}
		})
  }
  
  this.removeZombieFromMatterWorld = () => {
    this.dead = true
    // remove attack hit box in case the zombie was killed on that exact frame and
		// attack box didn't get removed 
		if (this.attackHitbox) {
			World.remove(world, this.attackHitbox)
    }
    if (this.healthDisplay) {
      container.removeChild(this.healthDisplay)
    }
		// if health is depleted, remove all enemy sprites and bodies
		this.sprites.forEach(sprite => { container.removeChild(sprite) })
    if (this.attackHitbox) { World.remove(world, this.attackHitbox) }
		World.remove(world, this.composite)
    return 'zombie removed'
  }

  this.removeZombieFromZombiesArray = () => {
    let idx = zombies.indexOf(this)
    if (idx > -1) {
      zombies.splice(idx, 1)
    }
  }
  
	this.kill = (partLastDamaged, skipZombieRag, killType) => {
		if (this.attackHitbox) {
			World.remove(world, this.attackHitbox)
    }
    if (!this.dead) {
      this.dead = true
			this.health = 0
			if (this.healthDisplay) {
				container.removeChild(this.healthDisplay)
      }
      // if (this.criticalText) {
      //   container.removeChild(this.criticalText)
      // }
      if (this.shadow) {
        container.removeChild(this.shadow)
      }
			container.removeChild(this.shadow)
      this.removeZombieFromMatterWorld()
      this.removeZombieFromZombiesArray()
      let force = { x: this.directionX ? -0.2 : 0.2, y: -0.25 }
			let dropPos = {
				x: this.directionX ? this.body.position.x - 20 : this.body.position.x + 20,
				y: this.body.position.y - 60
			}
			if (!skipZombieRag) {
				GenerateRandomCreditDrops(dropPos)
				if (getState().game.player.perks['ammoDropper'].owned) {
					let { list } = getState().game.player.weapons
					new AmmoDrop(dropPos, list[Math.floor(Math.random() * list.length)], random.int(4, 16))
				}
				if (getState().game.player.perks['healthDropper'].owned) {
					new HealthDrop(random.int(2, 6), dropPos)
				}
				bulletImpacts.push(
					new BulletImpact('enemy', this.body.position, {
						duration: 3,
						alphaDuration: 3,
						set: 1,
						rand: {
							min: 1, max: 4
						}
					})
				)
			}
			if (!skipZombieRag) {
        dispatch(decrementWaveCountToDefeat())
				let scoreAmt = getState().game.scoreAmounts.zombie
				if (partLastDamaged === 'head') {
					scoreAmt += getState().game.scoreAmounts.headShot
				}
				if (Date.now() - JSON.parse(getState().game.zombieLastKilled) < 1000) {
					createTextMessage('kill streak bonus!', '#FF5500', .35, .75)
					scoreAmt += getState().game.scoreAmounts.zombieStreakBonus
				}
				if (killType && killType === 'fell off') {
					if (!getState().game.achievements[AchievementDefs.THIS_IS_SPARTA].owned) {
						dispatch(setAchievementOwned( AchievementDefs.THIS_IS_SPARTA ))
						updateScore( getState().game.scoreAmounts.achievement )
					}
					createTextMessage('knockoff bonus!', '#FF5500', .35, .75)
					scoreAmt += getState().game.scoreAmounts.zombieKnockedOffStage
				}
				dispatch(
					setZombieLastKilled( JSON.stringify(Date.now()) )
				)
				if (killType === 'grenade explosion') {
					dispatch(
						setLastGrenadeKill( JSON.stringify(Date.now()) )
					)
					setTimeout(() => {
						if (Date.now() - JSON.parse(getState().game.lastGrenadeKill < 100)) {
							if (getState().game.consecutiveGrenadeKills > 2) {
								if (!getState().game.achievements[ AchievementDefs.FIRE_IN_THE_HOLE ].owned) {
									dispatch(setAchievementOwned( AchievementDefs.FIRE_IN_THE_HOLE ))
									updateScore( getState().game.scoreAmounts.achievement )
								}
							}
						}
						dispatch( resetConsecutiveGrenadeKills() )
					}, 0)
				}
				if (killType === 'landmine') {
					dispatch(
						increaseStats( StatsDefs.mineKills )
					)
					setTimeout(() => {
						if (getState().game.stats.mineKills > 2 && !getState().game.achievements[ AchievementDefs.MINER ].owned) {
							dispatch(setAchievementOwned(AchievementDefs.MINER))
							updateScore(getState().game.scoreAmounts.achievement)
						}
					}, 0)
				}
				if (killType === 'turret') {
					dispatch(
						increaseStats( StatsDefs.turretKills )
					)
					setTimeout(() => {
						if (getState().game.stats.turretKills > 9 && !getState().game.achievements[ AchievementDefs.GUNNA ].owned) {
							dispatch(setAchievementOwned(AchievementDefs.GUNNA))
							updateScore(getState().game.scoreAmounts.achievement)
						}
					}, 0)
				}
				if (
						getState().game.player.variables.jumping &&
						!getState().game.achievements[ AchievementDefs.THE_JUMP_SHOT ].owned &&
						killType !== 'grenade explosion' &&
						killType !== 'landmine' &&
						killType !== 'landmine explosion' &&
						killType !== 'clustermine explosion'
					) {
          	dispatch(
            	setAchievementOwned( AchievementDefs.THE_JUMP_SHOT )
          	)
        }
				updateScore( scoreAmt )
				dispatch(
					increaseStats( StatsDefs.zombiesKilled )
				)
				new ZombieRagdoll({
					x: this.body.position.x,
					y: this.body.position.y - getBodyHeight(this.body)
				}, force, partLastDamaged)
			}
      if (this.attackHitbox) {
        World.remove(world, this.attackHitbox)
      }
			zombies = 1
			zombies = null
      // container = 1
			// container = null
    }
	}

	// const getRandomInt = () => random.int(2, 5)

	const makeAttackHitbox = (num) => {
		if (this.playerBodyPosition) {
			let comparison = (this.playerBodyPosition.x > this.body.position.x)
			let compareResult = comparison ? 80 : -80
			return Bodies.rectangle(this.body.position.x + compareResult, this.body.position.y - getBodyHeight(this.body)/2, num === 2 ? 120 : 100, 150, {
				label: 'enemy zombie attack',
				isStatic: true
			})
		}
	}

	// zAttack.onFrameChange = () => {
	// 	if (zAttack.currentFrame === 6) {
	// 		this.playSlashAnim()
	// 	}
	// 	if (zAttack.currentFrame === 8) {
	// 		World.add(world, this.attackHitbox)
	// 	}
	// 	if (zAttack.currentFrame === 9) {
	// 		World.remove(world, this.attackHitbox)
	// 		// this.attackHitbox = null
	// 	}
	// }
	zAttack.onFrameChange = () => {
		idleAnim.visible = false
	}
	zAttack.onComplete = () => {
		zAttack.gotoAndStop(0)
		zAttack.visible = false
		this.playWalkAnim()
		World.remove(world, this.attackHitbox)
	}
	zAttack2.onComplete = () => {
		zAttack2.gotoAndStop(0)
		zAttack2.visible = false
		this.playWalkAnim()
		World.remove(world, this.attackHitbox)
	}
	//hitbox will be a matter body that appears for a few frames
	this.timeSinceLastAttack = Date.now() + 500
	let zombieGrowl = gameObjects.getKeyValue('zombie-growl-sound')
	this.attack = () => {
		if (!this.paused && zombies.indexOf(this) > -1 && !this.player.dead && !this.dead) {
			this.timeSinceLastAttack = Date.now()
			let oneOfTwo = [ 1, 2 ][ Math.floor( Math.random() * 2 ) ]
			this.attackHitbox = makeAttackHitbox(oneOfTwo)
			zombieGrowl.play()
			// change frames to an attack animation here
			zWalk.visible = false
			if (oneOfTwo === 1) {
				zAttack.visible = true
				zAttack.play()
			} else {
				zAttack2.visible = true
				zAttack2.play()
			}
			idleAnim.visible = false
			delayedCall(.15, () => {
				this.playSlashAnim()
			})
			delayedCall(.2, () => {
				World.add(world, this.attackHitbox)
				delayedCall(.1, () => {
					World.remove(world, this.attackHitbox)
				})
			})
		} else {
			if (this.attackHitbox) {
				World.remove(world, this.attackHitbox)
				// this.attackHitbox = null
			}
		}
  }

  this.pause = () => {
    this.paused = true
    // this.stopWalkAnim()
    idleAnim.stop()
    this.walkAnim.stop()
    this.attackAnim.stop()
    this.attackAnim2.stop()
  }

  this.unpause = () => {
    this.paused = false
    this.walkAnim.play()
    if (zAttack.currentFrame !== 0) {
      zAttack.play()
    }
  }

  zombies.push(this)

  return this
}
