import * as PIXI from 'pixi.js'
import { Mouse, MouseConstraint, World, Engine, Render, Runner, Composite, Vector } from 'matter-js'
import store from '../../../app/store'
import random from 'random'

const width = 1200, height = 550
const worldWidth = 6000

const { getState } = store

const { TweenLite: { to } } = window

export const applyGsapHover = pixiElt => {
	pixiElt.on('mouseover', () => {
		to(pixiElt.scale, .35, { x: 1.05, y: 1.05 })
	})
	pixiElt.on('mouseout', () => {
		to(pixiElt.scale, .35, { x: 1.00, y: 1.00 })
	})
}

export const makePixiSprite = (stringIdentifier) => {
	return (
		new PIXI.Sprite(
			PIXI.Texture.from(
				getState().game.textures[stringIdentifier]
			)
		)
	)
}

export const calcBulletAngle = (position1, position2) => {
  let { game: { player: { weapons: { bulletVariance, equipped, bulletForce } } } } = getState()
	let angle = Vector.angle(position1, position2)
	let bulletAngle = {
		x: Math.cos(angle) * bulletForce[equipped],
		y: (Math.sin(angle) * bulletForce[equipped]) + random.float(-bulletVariance[equipped], bulletVariance[equipped])
	}
	return bulletAngle
}

export const calcGrenadeAngle = (position1, position2, force) => {
  let angle = Vector.angle(position1, position2)
	let grenadeAngle = {
		x: Math.cos(angle) * force.x,
		y: Math.sin(angle) * force.y
	}
	return grenadeAngle
}

export const getBodyWidth = body => {
	return (body.bounds.max.x - body.bounds.min.x)
}

export const getBodyHeight = body => {
	return (body.bounds.max.y - body.bounds.min.y)
}

export const getPlayerPartByType = (type, gameObjects) => {
	let {
		game: {
			player: {
				composite: {
					headId,
					bodyId,
					armId,
					gunId
				}
			}
		}
	} = getState()
	let world = gameObjects.getKeyValue('world')
	if (type === 'head') {
		return Composite.get(world, headId, 'body')
	}
	if (type === 'body') {
		return Composite.get(world, bodyId, 'body')
	}
	if (type === 'arm') {
		return Composite.get(world, armId, 'body')
	}
	if (type === 'gun') {
		return Composite.get(world, gunId, 'body')
	}

}

