export const getPlayerGun = (gameObjects) => {
	let world = gameObjects.getKeyValue('world')
	let { game: { player: { composite: { gunId } } } } = getState()
	return Composite.get(world, gunId, 'body')
}

export const getPlayerBody = (gameObjects) => {
	let world = gameObjects.getKeyValue('world')
	let { game: { player: { composite: { bodyId } } } } = getState()
	return Composite.get(world, bodyId, 'body')
}

export const getPlayerComposite = (gameObjects) => {
	let world = gameObjects.getKeyValue('world')
	let { game: { player: { compositeId } } } = getState()
	return Composite.get(world, compositeId, 'composite')
}

export function setUpPixi(elementId) {
	let app = new PIXI.Application({
		width,
		height,
		antialias: false,
    resolution: 1
	})
	PIXI.settings.ROUND_PIXELS = true;
	document.getElementById(elementId).appendChild(app.view)
	// app.view.style.opacity = .85

	return {
		app,
		loader: new PIXI.Loader()
	}

}

export function setUpMatter(elementId, mouseConstraintBool) {
	// create engine
	let engine = Engine.create()
	let world = engine.world
	world.bounds = {
		min: { x: -worldWidth, y: height * -2 },
		max: { x: worldWidth, y: height * 1.5 }
	}

	// create renderer
	let render = Render.create({
			element: document.getElementById(elementId),
			engine: engine,
			options: {
				width,
				height,
				pixelRatio: .5
			}
	})
	Render.run(render)

	// Render.lookAt(render, {
	// 	min: { x: 0, y: 0 },
	// 	max: { x: width, y: height }
	// })

	// create runner
	let runner = Runner.create()
	Runner.run(runner, engine)

	if (mouseConstraintBool) {
		let mouse = Mouse.create(render.canvas)
		let	mouseConstraint = MouseConstraint.create(engine, {
			mouse: mouse,
			constraint: {
				stiffness: 0.2,
				render: {
					visible: false
				}
			}
		})
		World.add(world, mouseConstraint)
		// keep the mouse in sync with rendering
		render.mouse = mouse
	}

	// World.add(world, Bodies.rectangle(0,0,20,20))

	return {
		engine,
		render,
		runner,
		world,
		width,
		height,
		worldWidth
		// mouse,
		// mouseConstraint
	}

}
