import { Graphics } from 'pixi.js'
import { Bodies, Body, World } from 'matter-js'
import { BevelFilter } from '@pixi/filter-bevel'
import { itemCollisionOptions, playerCollisionOptions } from '../collisionOptions'
import { GlowFilter } from '@pixi/filter-glow'
import store from '../../../app/store'

export default class HealthDrop {

  constructor(amt, pos) {
    this.container = window.gameObjects.getKeyValue('game-pixi-container')
    let _amt = amt * 3
    this.gfc = new Graphics()
    this.gfc.lineStyle(2, 0x0)
    this.gfc.beginFill(0x1CB461)
    this.gfc.drawRect(0, 0, _amt/2, _amt)
    this.gfc.drawRect(-_amt/4, _amt/4, _amt, _amt/2)
    this.gfc.lineStyle(0, 0x0, 0)
    this.gfc.beginFill(0x1CB461)
    this.gfc.drawRect(0, 0, _amt/2, _amt)
    this.gfc.drawRect(-_amt/4, _amt/4, _amt, _amt/2)
    this.bevel = new BevelFilter({ thickness: 3, lightAlpha: .5, shadowAlpha: .5, shadowColor: 0x247F4D })
    this.glow = new GlowFilter({ outerStrength: .5 })
    this.gfc.filters = store.getState().game.filterEffects ? [
      this.glow, this.bevel,
    ] : []

    this.body = Bodies.rectangle(pos.x, pos.y, _amt, _amt, {
      restitution: .65,
      collisionFilter: itemCollisionOptions
    })
    this.body.main = this
    this.body.amount = amt
    this.body.label = 'health drop'
    World.add(window.gameObjects.getKeyValue('world'), this.body)
    window.gameObjects.getKeyValue('health-drops').push( this )
    
    return this
  }

  checkFilters() {
    if (this.gfc && store.getState().game.filterEffects) {
      this.gfc.filters = [ this.bevel, this.glow ]
    } else {
      if (this.gfc) {
        this.gfc.filters = []
      }
    }
  }

  destroy() {
    let healths = window.gameObjects.getKeyValue('health-drops')
    let idx = healths.indexOf(this)
    if (idx > -1) { healths.splice(idx, 1) }
    this.gfc.destroy()
    this.gfc = null
    World.remove(window.gameObjects.getKeyValue('world'), this.body)
    this.body = null
    healths = null
    idx = null
    this.container = null
  }

  renderPosition(renderFn) {
    if (this.container && this.container.children.indexOf(this.gfc) === -1) {
      this.container.addChild(this.gfc)
    }
    this.gfc.position = renderFn(this.body)
  }

}