import { Bodies, World } from 'matter-js'
import { Graphics, Text, Container, filters } from 'pixi.js'
import { itemCollisionOptions } from '../collisionOptions'
import store from '../../../app/store'
import { BevelFilter } from '@pixi/filter-bevel'
import { GlowFilter } from '@pixi/filter-glow'

export default function AmmoDrop (position, weaponType, amount) {
  let { BlurFilter } = filters  
  let { gameObjects } = window
  let container = gameObjects.getKeyValue('game-pixi-container')
  let ammoDrops = gameObjects.getKeyValue('ammo-drops-by-type')

  let colors = {
    'pistol weapon': 0x0254FF,
    'smg weapon': 0xFF9D02,
    'shotgun weapon': 0xA52121,
  }

  let text = {
    'pistol weapon': 'PISTOL',
    'smg weapon': 'SMG',
    'shotgun weapon': 'SHOTGUN',
  }
  
  let props = {
    x: position.x, y: position.y,
    w: 50, h: 20
  }

  let ammoBox = Bodies.rectangle(props.x, props.y, props.w, props.h, {
    collisionFilter: itemCollisionOptions
  })
  ammoBox.label = 'ammo drop'
  ammoBox.ammo_type = weaponType
  ammoBox.amount = amount
  let world = gameObjects.getKeyValue('world')
  World.add(world, ammoBox)

  ammoBox.main = this

  let ammoGfc = new Container()

  let boxGfc = new Graphics()
  boxGfc.beginFill(colors[weaponType])
  boxGfc.drawRect(0, 0, props.w, props.h)
  boxGfc.pivot.x = boxGfc.width * .5
  boxGfc.pivot.y = boxGfc.height * .5
  let bevelFilter = new BevelFilter()
  bevelFilter.thickness = 5
  bevelFilter.lightColor = 0xffffff
  bevelFilter.lightAlpha = 0.25
  bevelFilter.shadowColor = 0x0
  bevelFilter.shadowAlpha = 0.25
  boxGfc.filters = store.getState().game.filterEffects ? [
    bevelFilter
  ] : []

  let blurGfc, blurFilter
  blurGfc = new Graphics()
  blurGfc.beginFill(0x000000)
  blurGfc.drawRect(0, props.h/2, props.w, props.h/2)
  blurGfc.pivot.x = blurGfc.width * .5
  blurGfc.pivot.y = blurGfc.height * .5
  blurFilter = new BlurFilter(5, 3)
  blurGfc.alpha = .85
  blurGfc.filters = store.getState().game.filterEffects ? [
    blurFilter
  ] : []
  // if (store.getState().game.filterEffects) {
  // }

  let mask = new Graphics()
  mask.beginFill(colors[weaponType])
  mask.drawRect(0, 0, props.w, props.h)
  mask.pivot.x = mask.width * .5
  mask.pivot.y = mask.height * .5
  blurGfc.mask = mask

  if (store.getState().game.filterEffects) {
    ammoGfc.addChild(boxGfc, blurGfc, mask)
  } else {
    ammoGfc.addChild(boxGfc, mask)
  }

  let boxTxt = new Text(text[weaponType], { fontSize: '9px', fill: '#FFF', fontWeight: 'bold' })
  boxTxt.pivot.x = boxTxt.width * .5
  boxTxt.pivot.y = boxTxt.height * .5
  let glow = new GlowFilter()
  glow.color = 0x0
  glow.outerStrength = 1.0
  boxTxt.filters = store.getState().game.filterEffects ? [ glow ] : []
  ammoGfc.addChild(boxTxt)

  // container.addChild(ammoGfc)

  this.gfc = ammoGfc
  this.body = ammoBox

  this.checkFilters = () => {
    if (store.getState().game.filterEffects) {
      ammoGfc.addChild(blurGfc, boxTxt)
      boxGfc.filters = [ bevelFilter ]
      blurGfc.filters = [ blurFilter ]
      boxTxt.filters = [ glow ]
    } else {
      if (ammoGfc.children.indexOf(blurGfc) > -1) {
        ammoGfc.removeChild(blurGfc)
      }
      boxGfc.filters = []
      blurGfc.filters = []
      boxTxt.filters = []
    }
  }

  this.destroy = () => {
    const idx = ammoDrops[weaponType].indexOf(this)
    if (idx > -1) {
      ammoDrops[weaponType].splice(idx, 1)
      ammoGfc.destroy()
      World.remove(world, ammoBox)
    }
  }
  
  // auto time-out to destroy self 
  setTimeout(() => {
    this.destroy()
  }, 20000)

  ammoDrops[weaponType].push(this)

  this.positionAmmoBox = (renderFn) => {
    let { x, y, rotation: rot } = renderFn(ammoBox)
    ammoGfc.position = { x, y }
    ammoGfc.rotation = rot
    if (container && container.children.indexOf(this.gfc) === -1) {
      container.addChild(ammoGfc)
    }
  }

  return this

}