import { World } from 'matter-js'
import { AnimatedSprite } from 'pixi.js'

export const BLOOD_SPURT_TYPE_1 = 'blood spurt type 1'
export const BLOOD_SPURT_TYPE_2 = 'blood spurt type 2'
export const BLOOD_SPURT_TYPE_3 = 'blood spurt type 3'

let { TweenLite: { set } } = window

export default function BloodSpurt (type, position, scale, bodyToDelete) {

  let { gameObjects } = window
  let world = gameObjects.getKeyValue('world')
  let container = gameObjects.getKeyValue('game-pixi-container')

  let chosenSpurt = null
  
  let bloodSpurt1_sheet = gameObjects.getKeyValue('blood-spurt-1')
  let bloodSpurt1 = new AnimatedSprite( bloodSpurt1_sheet.animations['blood_spurt_1'] )
  
  let bloodSpurt2_sheet = gameObjects.getKeyValue('blood-spurt-2')
  let bloodSpurt2 = new AnimatedSprite( bloodSpurt2_sheet.animations['blood_spurt_2'] )
  
  let bloodSpurt3_sheet = gameObjects.getKeyValue('blood-spurt-3')
  let bloodSpurt3 = new AnimatedSprite( bloodSpurt3_sheet.animations['blood_spurt_3'] )
  
  // randomize the returned spurt
  let spurts = [ BLOOD_SPURT_TYPE_1, BLOOD_SPURT_TYPE_2, BLOOD_SPURT_TYPE_3 ]
  let autoChosen = spurts[ Math.floor( Math.random() * spurts.length ) ]
  
  if (!type) {
    type = autoChosen
  }
  
  if (type === BLOOD_SPURT_TYPE_1) {
    chosenSpurt = bloodSpurt1
  } else if (type === BLOOD_SPURT_TYPE_2) {
    chosenSpurt = bloodSpurt2
  } else {
    chosenSpurt = bloodSpurt3
  }

  chosenSpurt.onComplete = () => {
    chosenSpurt.destroy()
    if (bodyToDelete) {
      World.remove(world, bodyToDelete)
    }
    world = null
    container = null
    gameObjects = null
    chosenSpurt = null
    bloodSpurt1 = null
    bloodSpurt1_sheet = null 
    bloodSpurt2 = null
    bloodSpurt2_sheet = null 
    bloodSpurt3 = null
    bloodSpurt3_sheet = null 
    spurts = null
    autoChosen = null
  }

  chosenSpurt.animationSpeed = .5
  chosenSpurt.pivot.x = chosenSpurt.width * .5
  chosenSpurt.pivot.y = chosenSpurt.height * .5
  if (scale) {
    set(chosenSpurt.scale, { x: scale, y: scale })
  } else {
    set(chosenSpurt.scale, { x: .5, y: .5 })
  }
  chosenSpurt.position = position
  chosenSpurt.loop = false
  chosenSpurt.updateAnchor = true
  chosenSpurt.play()
  container.addChild( chosenSpurt )

  return chosenSpurt

}