import * as PIXI from 'pixi.js'
import { applyGsapHover } from '../utils/Utils'
import { gameStateTransition, unsetAllKeys } from '../gameSlice'
import GameStates, { SubStates } from '../constants/GameStates'
import store from '../../../app/store'
import { GenerateRandomAmmoDrops, GenerateRandomCreditDrops } from '../items/ItemGenerators'
import { createGenericMessage } from '../createMessages'
import { FELL_OFF } from '../constants/DeathTypes'
import random from 'random'

const { dispatch, getState } = store

function RespawnButton () {
	let container = new PIXI.Container()
	let btn = new PIXI.Graphics()
	btn.beginFill(0x555555)
	btn.drawRect(0, 0, 150, 50)
	container.addChild(btn)

	const text = new PIXI.Text('Respawn?', { font: 'Arial', fill: '#FFFFFF', fontSize: 20, fontWeight: 'bold' })
	text.position.x = btn.width/2 - text.width/2
	text.position.y = btn.height/2 - text.height/2
	container.addChild(text)

	return container
}

export default function ScreenUI () {
	let { gameObjects, TweenLite: { from }, Bounce } = window
  let gameContainer = gameObjects.getKeyValue('game-pixi-container')
  let engine = gameObjects.getKeyValue('engine')

  // let zombies = gameObjects.getKeyValue('zombies')

	this.respawnButton = new RespawnButton()
	gameContainer.addChild(this.respawnButton)
	this.respawnButton.visible = false
	this.respawnButton.interactive = true
	this.respawnButton.pivot.x = this.respawnButton.width/2
	this.respawnButton.pivot.y = this.respawnButton.height/2
	this.respawnButton.position.x = (this.respawnButton.width/2) + 20
	this.respawnButton.position.y = (this.respawnButton.height/2) + 20

  this.respawn = () => {
    let player = gameObjects.getKeyValue('player')
    player.respawn()
    createGenericMessage('Still alive!', 0xFFFFFF, .5, .75)
    if (player.lastDeathType !== FELL_OFF) {
      GenerateRandomAmmoDrops({
        x: random.float(player.body.position.x - 100, player.body.position.x + 100),
        y: player.body.position.y - 200,
      },
      {
        min: 10, max: getState().game.player.weapons.ammoCapacity[getState().game.player.weapons.equipped] * 2
      },
      random.int(1, 2))
      GenerateRandomCreditDrops({
        x: random.float(player.body.position.x - 100, player.body.position.x + 100),
        y: player.body.position.y - 200,
      })
    }
  }

	this.respawnButton.on('click', () => {
		this.respawn()
		this.respawnButton.visible = false
	})

	applyGsapHover(this.respawnButton)

	this.showRespawnButton = () => {
		this.respawnButton.visible = true
		from(this.respawnButton.scale, 1.5, { x: .5, y: .5, ease: Bounce.easeOut })
  }

  const unpause = () => {
    // zombs
    let zombies = gameObjects.getKeyValue('zombies')
    // player 
    let player = gameObjects.getKeyValue('player')
    player.pause()
    // zombies 
    zombies.forEach(zombie => {
      zombie.unpause()
    })
    // resume physics engine
    engine.timing.timeScale = 1
  }

  const pause = () => {
    // zombs
    let zombies = gameObjects.getKeyValue('zombies')
    dispatch( unsetAllKeys() )
    // character
    let player = gameObjects.getKeyValue('player')
    player.unpause()
    // zombies
    zombies.forEach(zombie => {
      zombie.pause()
    })
    // pause physics engine
    engine.timing.timeScale = 0.05
  }

  this.unpause = unpause
  this.pause = pause
  
  this.togglePause = () => {
    let { gameState, gameSubState } = getState().game
    if (gameState !== GameStates.MAIN_MENU && gameState !== GameStates.SHOP_MENU && gameSubState !== SubStates.BUILD_PLACEMENT_MODE && gameState !== GameStates.BUILD_MENU && gameSubState !== SubStates.SHOP_QUERY_SUBMENU && gameSubState !== SubStates.SCORE_ENTRY && gameSubState !== SubStates.WAVE_DEFEATED) {
      let player = gameObjects.getKeyValue('player')
      if (!player.dead) {
        if (gameState !== GameStates.PAUSE && gameSubState !== SubStates.WAITING) {
          dispatch( gameStateTransition('main', gameState, GameStates.PAUSE) )
          pause()
        } else {
          dispatch( gameStateTransition('main', gameState, GameStates.GAMEPLAY) )
          if (gameSubState === SubStates.WAVE_STARTED) {
            dispatch( gameStateTransition('sub', gameSubState, SubStates.WAVE_STARTED) )
          } else if (gameSubState === SubStates.NEUTRAL) {
            dispatch( gameStateTransition('sub', gameSubState, SubStates.WAITING) )
          }
          unpause()
        }
      }
    }
  }
  
  // add a pause listener to the screen UI ('P' (80) or ESC (27) key)
  document.body.addEventListener('keydown', e => {
    if (e.key === 'p' || e.key === 'P' || e.key === 'Escape') {
      console.log('pause key was pressed')
      this.togglePause()
    }
  })

	return this

}
