import GrenadeExplosion from '../FX/GrenadeExplosion'
import { Bodies, World, Body, Vector } from 'matter-js'
import * as PIXI from 'pixi.js'
import store from '../../../app/store'
import { BevelFilter } from '@pixi/filter-bevel'
import { radians } from 'radians'
import { NoiseFilter } from '@pixi/filter-noise'
import { MotionBlurFilter } from '@pixi/filter-motion-blur'

export default function Grenade (x, y, r, force, fuseTime) {
  this.grenadeDistance = {
    start: x,
    end: null
  }
  let { getState } = store
  let { gameObjects, TweenLite: { delayedCall, from } } = window
  let world = gameObjects.getKeyValue('world')
  let container = gameObjects.getKeyValue('game-pixi-container')
  let grenades = gameObjects.getKeyValue('grenades')
  let grenadeBody = Bodies.circle(x, y, r, {
    restitution: .8,
    collisionFilter: { category: 3 }
  })
  grenadeBody.label = 'grenade'
  grenadeBody.main = this
  World.add(world, grenadeBody)
  Body.applyForce(grenadeBody, grenadeBody.position, force)
  grenadeBody.torque = .01
  let grenadeGfc = new PIXI.Graphics()
  grenadeGfc.beginFill(0x1F472C)
  grenadeGfc.drawCircle(0, 0, r)
  this.grenadeBody = grenadeBody
  let bevel = new BevelFilter()
  bevel.shadowAlpha = .45
  bevel.lightAlpha = .35
  bevel.thickness = 5
  bevel.shadowColor = 0x0
  bevel.lightColor = 0x208C44
  let noise = new NoiseFilter()
  noise.noise = .1
  noise.seed = .5
  grenadeGfc.filters = store.getState().game.filterEffects ? [ bevel, noise ] : []
  let motionBlur = new MotionBlurFilter({ velocity: { x: 0, y: 0 } })
  from(motionBlur.velocity, .75, { x: 100, y: 10 })
  container.addChild(grenadeGfc)
  this.animate = renderFn => {
    if (grenadeBody && grenadeGfc) {
      let { x, y, rotation: rot } = renderFn(grenadeBody)
      grenadeGfc.position.x = x
      grenadeGfc.position.y = y
      grenadeGfc.rotation = rot
      bevel.rotation = radians(rot)
      grenadeGfc.filters = store.getState().game.filterEffects ? [ bevel, noise, motionBlur ] : []
    }
  }

  delayedCall(fuseTime ? fuseTime : getState().game.player.weapons.grenadeFuseTime.default, () => {
    if (!this.exploded) {
      this.explode()
    }
  })
  this.exploded = false
  this.explode = (showRocks) => {
    this.grenadeDistance.end = this.grenadeBody.position.x
    this.exploded = true
    new GrenadeExplosion(grenadeBody.position, showRocks ? showRocks : null)
    let explosionSize = getState().game.player.weapons.grenadeExplosionSize
    this.explosionHitbox = Bodies.rectangle(grenadeBody.position.x, grenadeBody.position.y, explosionSize, explosionSize, {
      // isStatic: true
    })
    grenadeBody.distance = this.grenadeDistance
    this.explosionHitbox.label = 'grenade explosion'
    World.remove(world, grenadeBody)
    World.add(world, this.explosionHitbox)
    container.removeChild(grenadeGfc)
    delayedCall(.01, () => {
      this.destroy()
    })
  }
  this.destroy = () => {
    World.remove(world, this.explosionHitbox)
    let idx = grenades.indexOf(this)
    if (idx > -1) {
      grenades.splice(idx, 1)
    }
    // world = null
    // container = null
    // gameObjects = null
    // this.explosionHitbox = null
    // grenadeBody = null
    // grenadeGfc = null
  }
}