import { increaseScore, decreaseScore, updatePrevScore, flipScoreToggle } from '../gameSlice'
import store from '../../../app/store'


export const updateScore = amount => {
  const { dispatch } = store
  const { TweenLite: { delayedCall } } = window
  dispatch( increaseScore(amount) )
  dispatch( flipScoreToggle() )
  delayedCall(.25, () => {
    dispatch( updatePrevScore() )
  })
}

export const subtractScore = amount => {
  const { dispatch } = store
  const { TweenLite: { delayedCall } } = window
  dispatch( decreaseScore(amount) )
  dispatch( flipScoreToggle() )
  delayedCall(.25, () => {
    dispatch( updatePrevScore() )
  })
}
