import Zombie from '../enemies/Zombie'
import AmmoDrop from '../items/AmmoDrop'
import CreditDrop from '../items/CurrencyCredit'
import random from 'random'
import store from '../../../app/store'
import GameStates, { SubStates } from '../constants/GameStates'

const { getState } = store
const { TweenLite: { delayedCall } } = window
const yStartPos = getState().game.height/2

export const GenerateZombies = (count) => {
  const { gameObjects } = window
  let width = gameObjects.getKeyValue('world-width')
  let buffer = 150
  
  const startZombieCreation = () => {
    let numOnStage = (
      getState().game.waveNum > 20 ? 4
      : getState().game.waveNum > 10 ? 3
      : 2
    )
    for (let i = 0; i < count; ++i) {
      const getAZombie = () => {
        if (
          getState().game.zombiesOnStage < numOnStage &&
          getState().game.gameState !== GameStates.PAUSE &&
          getState().game.gameState !== GameStates.MAIN_MENU &&
          (getState().game.gameSubState === SubStates.WAVE_STARTED || getState().game.gameSubState === SubStates.PLAYER_RESPAWN)
          ) {
          console.log('should create a zombo')
          if (getState().game.gameSubState !== SubStates.WAVE_DEFEATED) {
            new Zombie(random.int(buffer, width - buffer), yStartPos)
          }
        } else {
          setTimeout(() => { getAZombie() }, 2000)
        }
      }
      getAZombie()
    }
  }
  startZombieCreation()
  
}

export const GenerateRandomAmmoDrops = (position, amountLimits, overrideCount) => {
  const { gameObjects } = window
  let width = gameObjects.getKeyValue('world-width')
  let buffer = 150
  let { game: { player: { weapons: { list, ammoCapacity } } } } = getState()
  let randCount = overrideCount ? overrideCount : random.int(1, 2)
  let type = list[Math.floor(Math.random() * list.length)]
  for (let i = 0; i < randCount; ++i) {
    new AmmoDrop(
      position ? position : { x: random.int(buffer, width - buffer), y: 200 },
      type,
      amountLimits ? random.int(amountLimits.min, amountLimits.max) : random.int(5, ammoCapacity[type]))
  }
}

export const GenerateRandomCreditDrops = (position, amountLimits) => {
  const { gameObjects } = window
  let width = gameObjects.getKeyValue('world-width')
  let buffer = 150
  let randCount = getState().game.player.perks['creditDropper'].owned ? random.int(5, 8) : random.int(4, 5)
  for (let i = 0; i < randCount; ++i) {
    new CreditDrop(
      position ?
        { x: position.x, y: position.y - random.int(0, 75) }
        : { x: random.int(buffer, width - buffer), y: 200 - random.int(0, 75) },
      amountLimits ? random.int(amountLimits.min, amountLimits.max) : getState().game.player.perks['creditDropper'].owned ? random.int(15, 30) : random.int(5, 20)
    )
  }
}