import { Bodies, World } from 'matter-js'
import { Sprite } from 'pixi.js'
import { playerCollisionOptions } from '../collisionOptions'
import random from 'random'

export default function GroundEffect (pos, type, bodyPart) {
  let {x,y} = pos
  let {gameObjects,TweenLite:{to,delayedCall}} = window
  let world = gameObjects.getKeyValue('world')
  let container = gameObjects.getKeyValue('game-pixi-container')
  let groundFx = gameObjects.getKeyValue('static-ground-effects')
  let fxSheet = gameObjects.getKeyValue('ground-fx-sheet')
  let bulletTextures = [
    'bullet-impact1.png',
    'bullet-impact2.png'
  ]
  let groundBloodSpatters = [
    'ground-blood-spatter1.png',
    'ground-blood-spatter2.png',
    'ground-blood-spatter3.png',
  ]
  const getRandomRoundBulletImpact = () => (
    new Sprite(
      fxSheet.textures[
        bulletTextures[ Math.floor( Math.random() * bulletTextures.length ) ]
      ]
  ))
  const getRandomBloodSpatter = () => (
    new Sprite(
      fxSheet.textures[
        groundBloodSpatters[ Math.floor( Math.random() * groundBloodSpatters.length ) ]
      ]
  ))
  
  let sprite
  if (type === 'bullet impact') {
    sprite = getRandomRoundBulletImpact()
  }
  if (type === 'blood spatter') {
    sprite = getRandomBloodSpatter()
  }
  sprite.pivot.x = sprite.width * .5
  sprite.pivot.y = sprite.height * (bodyPart ? .2 : .5)
  let ground = gameObjects.getKeyValue('ground')
  if (
    sprite.position.x - sprite.width > ground.bounds.min.x ||
    sprite.position.x + (sprite.width/2) < ground.bounds.max.x
    ) {
  }
  // re-add player sprites on-top
  let player = gameObjects.getKeyValue('player')
  if (!player.dead) {
    player.sprites.forEach(s => { container.addChild(s) })
  }

  let positionalBody = Bodies.rectangle(x,y,10,10,{isStatic:true,collisionFilter:playerCollisionOptions})
  World.add(world, positionalBody)

  this.renderPosition = renderFn => {
    sprite.position = renderFn(positionalBody)
    if (container && container.children.indexOf(sprite) === -1) {
      container.addChild(sprite)
    }
  }

  this.destroy = () => {
    World.remove(world, positionalBody)
    container.removeChild(sprite)
    let idx = groundFx.indexOf(this)
    if (idx > -1) {
      groundFx.splice(idx, 1)
    }
    world = null
    container = null
    gameObjects = null
    groundFx = null
    fxSheet = null
    bulletTextures = null
    groundBloodSpatters = null
    positionalBody = null
  }

  const tweenOut = () => {
    to(sprite, .5, {
      alpha: 0,
      onComplete: () => {
        this.destroy()
      }
    })
  }
  delayedCall(1, tweenOut)

  return this

}