import * as PIXI from 'pixi.js'
import store from '../../app/store'
import { gameStateTransition, setShowingMessage } from './gameSlice'
import GameStates, { SubStates } from './constants/GameStates'
import { getBodyHeight } from './utils/Utils'
import { resetWave } from './ReactComponents/PauseMenu'

const { getState, dispatch } = store

export const createWaveMessage = txt => {

	PIXI.BitmapFont.from('Arial', {
		fill: '#FFFFFF',
		fontSize: 40,
    fontWeight: 'bold'
  }, {
    chars: [
      ['a', 'z'],
      ['0', '9'],
      ['A', 'Z'],
      ' .!?'
    ]
  })

	let {
		gameObjects,
		TweenLite: { from, to },
		Power1
  } = window
  // let screenUI = gameObjects.getKeyValue('screen-ui')
  let player = gameObjects.getKeyValue('player')
	let container = gameObjects.getKeyValue('game-pixi-container')
	let gameWidth = gameObjects.getKeyValue('game-width')
	let gameHeight = gameObjects.getKeyValue('game-height')
	const text = new PIXI.BitmapText(txt, { font: 'Arial' })

	text.pivot.x = text.width/2
	text.pivot.y = text.height/2
	text.position.x = gameWidth/2
	text.position.y = gameHeight/5

  container.addChild(text)
  
  const transitionToShopQuery = () => {
    resetWave() // in case any zombies are roaming
    if (getState().game.gameState === GameStates.MAIN_MENU || getState().game.gameState === GameStates.PAUSE) {
      if (getState().game.gameSubState === SubStates.WAVE_DEFEATED || getState().game.gameSubState === SubStates.SHOP_QUERY_SUBMENU) {
        dispatch(gameStateTransition('sub', getState().game.gameSubState, SubStates.SHOP_QUERY_SUBMENU))
      } else {
        dispatch(gameStateTransition('sub', getState().game.gameSubState, SubStates.NEUTRAL))
      }
    } else {
      if (!player.dead) {
        dispatch(gameStateTransition('sub', getState().game.gameSubState, SubStates.SHOP_QUERY_SUBMENU))
        container.removeChild(text)
      } else {
        dispatch(gameStateTransition('sub', getState().game.gameSubState, SubStates.PLAYER_DEATH))
      }
    }
  }
  
  const waveMsgOutro = () => {
    if (getState().game.gameState === GameStates.PAUSE) {
      setTimeout(waveMsgOutro, 2000)
    } else {
      to(text, .75, {
        delay: 2,
        x: gameWidth * 1.5,
        ease: Power1.easeIn,
        onUpdate: () => { if (gameObjects.getKeyValue('zombies').length > 0) { resetWave() } },
        onComplete: transitionToShopQuery
      })
    }
  }
  
  const waveMsgIntro = () => {
    if (getState().game.gameState === GameStates.PAUSE) {
      setTimeout(waveMsgIntro, 2000)
    } else {
      from(text, .75, {
        x: -100,
        alpha: 0,
        // onUpdate: () => { resetWave() },
        onUpdate: () => { if (gameObjects.getKeyValue('zombies').length > 0) { resetWave() } },
        onComplete: waveMsgOutro
      })
    }
  }

  waveMsgIntro()

	return this
}

export const createGenericMessage = (txt, fillColor, durationMotion, durationLinger) => {

	PIXI.BitmapFont.from('Arial', {
		fill: fillColor,
		fontSize: 40,
		fontWeight: 'bold'
	})

	let {
		gameObjects,
		TweenLite: { from, to },
		Power1
	} = window
	let container = gameObjects.getKeyValue('game-pixi-container')
	let gameWidth = gameObjects.getKeyValue('game-width')
	let gameHeight = gameObjects.getKeyValue('game-height')
	const text = new PIXI.BitmapText(txt, { font: 'Arial' })

	text.pivot.x = text.width/2
	text.pivot.y = text.height/2
	text.position.x = gameWidth/2
	text.position.y = gameHeight/2

	container.addChild(text)

	from(text, durationMotion, {
		x: -100,
		alpha: 0,
		onStart: () => {
			dispatch( setShowingMessage(true) )
		},
		onComplete: () => {
			to(text, durationMotion, {
				delay: durationLinger,
				x: gameWidth * 1.5,
				ease: Power1.easeIn,
				onComplete: () => {
					dispatch( setShowingMessage(false) )
					container.removeChild(text)
				}
			})
		}
	})

	return this
}

export const createTextMessage = (txt, fill, durationMotion, durationLinger, fontSize) => {

  let {
    gameObjects,
    TweenLite: { to, set }
  } = window
  let container = gameObjects.getKeyValue('game-pixi-container')
  let texts = gameObjects.getKeyValue('text-messages')
  let playerBody = gameObjects.getKeyValue('player').body
  let msg = new PIXI.Text(txt, { fill, fontSize: fontSize ? fontSize : 13, fontWeight: 'bold' })
  msg.pivot.x = msg.width * .5
  msg.pivot.y = msg.height * .5
  container.addChild(msg)

  let { game: { player: { position: playerPosition } } } = getState()
  let initialY = playerPosition.y - (getBodyHeight(playerBody) * .65)

  texts.push(msg)

  set(msg, {
    x: playerPosition.x,
    y: initialY - (texts.length * 20)
  })

  to(msg, durationMotion, {
    x: playerPosition.x,
    y: initialY - (texts.length * 20) - 20,
    onComplete: () => {
      to(msg, durationMotion, {
        delay: durationLinger,
        alpha: 0,
        onComplete: () => {
          msg.destroy()
          let idx = texts.indexOf(msg)
          if (idx > -1) {
            texts.splice(idx, 1)
          }
        }
      })
    }
  })

}
