import { AnimatedSprite } from 'pixi.js'
import * as PIXI from 'pixi.js'
import { AdjustmentFilter } from '@pixi/filter-adjustment'
import { GlowFilter } from '@pixi/filter-glow'
import random from 'random'
import store from '../../../app/store'

export default class Spark {

  constructor (type, renderBody, options) {
    let spark1 = new AnimatedSprite(
      window.gameObjects.getKeyValue('sparks-sheet').animations['spark-particle']
    )
    let spark2 = new AnimatedSprite(
      window.gameObjects.getKeyValue('muzzle-sparks-sheet').animations['muzzleSparks']
    )
    let sparks = [ spark1, spark2 ]
    sparks.forEach(spark => {
      let scale = options && options.scale ? options.scale : 1.0
      if (scale !== 1.0) {
        spark.scale.x = scale
        spark.scale.y = scale
      }
      spark.pivot.x = spark.width  * .5
      spark.pivot.y = spark.height * .5
      spark.loop = false
      spark.animationSpeed = options && options.animationSpeed ? options.animationSpeed : 1.0
      spark.onComplete = () => {
        this.container.removeChild(spark)
        this.container = null
        spark = null
        spark1 = null
        spark2 = null
        sparks = null
      }
    })
    this.container = window.gameObjects.getKeyValue('game-pixi-container')
    switch (type) {
      case 1:
        this.spark = spark1
        if (options && options.tint) {
          this.spark.tint = options.tint
        }
        break;
      case 2:
        this.spark = spark2
        this.blurFilter = new PIXI.filters.BlurFilter(random.float(0.5, 0.75), 5)
        this.adjFilter = new AdjustmentFilter()
        this.glowFilter = new GlowFilter()
        this.adjFilter.saturation = 2.0
        this.adjFilter.brightness = 1.5
        // glowFilter.outerStrength = 1.0
        this.glowFilter.color = 0xFFFFFF //0xFFD100 //0xFF9C00 //0xFFFFFF
        this.filters = store.getState().game.filterEffects ? [
          this.blurFilter,
          this.adjFilter,
          this.glowFilter
        ] : []
        this.spark.filters = this.filters
        break;
        default:
    }
    if (options && options.scale) {
      let sparkContainer = new PIXI.Container()
      this.spark.scale.x = options.scale
      this.spark.scale.y = options.scale
      sparkContainer.addChild(this.spark)
      sparkContainer.pivot.x = sparkContainer.width * (options.scale * .5)
      sparkContainer.pivot.y = sparkContainer.height * (options.scale * .5)
      this.spark = sparkContainer
    }

    this.renderBody = renderBody
    if (!options || (options && !options.stop)) {
      if (this.spark.play) {
        this.spark.play()
      }
      if (this.spark.children && this.spark.children[0]) {
        this.spark.children[0].play()
      }
    }
    return this
  }

  getFilters() {
    return {
      glow: this.glowFilter,
      adjust: this.adjFilter,
      blur: this.blurFilter
    }
  }

  updateFilters(filters) {
    this.filters = filters
    this.spark.filters = this.filters
  }

  renderPosition(renderFn) {
    if (this.renderBody) {
      this.spark.position = renderFn(this.renderBody)
      if (this.container && this.container.children.indexOf(this.spark) === -1) {
        this.container.addChild(this.spark)
      }
    }
  }

}